import Vue from 'vue'
import Router from 'vue-router'
//动态模块
const Dynamic = r => require.ensure([], () => r(require('../components/dynamic/page/Dynamic.vue')), 'dynamic')

//消息模块
const News = r => require.ensure([], () => r(require('../components/massage/page/News.vue')), 'news')
const CompanyNotice = r => require.ensure([], () => r(require('../components/massage/page/CompanyNotice.vue')), 'notice')
const ArticleDetails = r => require.ensure([], () => r(require('../components/massage/page/ArticleDetails.vue')), 'articledetails')
const LittleAssistant = r => require.ensure([], () => r(require('../components/massage/page/LittleAssistant.vue')), 'littleassistant')

//CRM板块
import List from '@/components/crm/page/List'
import AddData from '@/components/crm/page/AddData'
const CRM = r => require.ensure([], () => r(require('../components/crm/page/CRM.vue')), 'crm')
const CRMSearch = r => require.ensure([], () => r(require('../components/crm/page/CRMSearch.vue')), 'crmsearch')
const CRMMainDetail = r => require.ensure([], () => r(require('../components/crm/page/CRMMainDetail')), 'maindetail')
const SelectList = r => require.ensure([], () => r(require('../components/crm/page/SelectList.vue')), 'selectlist')
const MoneyHistory = r => require.ensure([], () => r(require('../components/crm/page/MoneyHistory.vue')), 'moneyhistory')
import CRMDetail from '@/components/crm/page/CRMDetail'
import AddRelate from '@/components/crm/page/AddRelate'
import FilePreview from '@/components/crm/page/FilePreview'
const RelevanceAProduct = r => require.ensure([], () => r(require('../components/crm/page/RelevanceAProduct.vue')), 'relevanceaproduct')
const MoreSort = r => require.ensure([], () => r(require('../components/crm/page/MoreSort.vue')), 'moresort')
//财务
const FinanceMain = r => require.ensure([], () => r(require('../components/crm/finance/FinanceMain.vue')), 'financemain')
const FinanceAdd = r => require.ensure([], () => r(require('../components/crm/finance/FinanceAdd.vue')), 'financeadd')
const FinanceOpinion = r => require.ensure([], () => r(require('../components/crm/finance/FinanceOpinion.vue')), 'financeopinion')

//办公板块
const Office = r => require.ensure([], () => r(require('../components/office/page/Office.vue')), 'office')
const AddressList = r => require.ensure([], () => r(require('../components/Office/page/AddressList.vue')), 'addresslist')
const LogList = r => require.ensure([], () => r(require('../components/office/page/LogList.vue')), 'loglist')
const LogDetail = r => require.ensure([], () => r(require('../components/office/page/LogDetail.vue')), 'logdetail')
const CreateLog = r => require.ensure([], () => r(require('../components/office/page/CreateLog.vue')), 'createlog')
const CreateComment = r => require.ensure([], () => r(require('../components/office/page/CreateComment.vue')), 'createcomment')
const Schedule = r => require.ensure([], () => r(require('../components/office/page/Schedule.vue')), 'schedule')
const CreateSchedule = r => require.ensure([], () => r(require('../components/office/page/CreateSchedule.vue')), 'createschedule')
const CommonDetail = r => require.ensure([], () => r(require('../components/office/page/CommonDetail.vue')), 'conmmondetail')
const FieldSign = r => require.ensure([], () => r(require('../components/office/page/FieldSign.vue')), 'fieldsign')
const TeamSign = r => require.ensure([], () => r(require('../components/office/page/TeamSign.vue')), 'teamsign')
const CreateSign = r => require.ensure([], () => r(require('../components/office/page/CreateSign.vue')), 'createsign')
const SignHistory = r => require.ensure([], () => r(require('../components/office/page/SignHistory.vue')), 'signhistory')
const MapChoice = r => require.ensure([], () => r(require('../components/office/page/MapChoice.vue')), 'mapchoice')
const RangeChoice = r => require.ensure([], () => r(require('../components/office/page/RangeChoice.vue')), 'rangechoice')
const OfficeWork = r => require.ensure([], () => r(require('../components/office/page/OfficeWork.vue')), 'officework')
const CheckStatistics = r => require.ensure([], () => r(require('../components/office/page/CheckStatistics.vue')), 'checkstatistics')
const CheckMonth = r => require.ensure([], () => r(require('../components/office/page/CheckMonth.vue')), 'checkmonth')
const CheckSet = r => require.ensure([], () => r(require('../components/office/page/CheckSet.vue')), 'checkset')

//审批
const ExamineMain = r => require.ensure([], () => r(require('../components/office/examine/ExamineMain.vue')), 'examinemain')
const ExamineList = r => require.ensure([], () => r(require('../components/office/examine/ExamineList.vue')), 'examinelist')
const ExamineMyList = r => require.ensure([], () => r(require('../components/office/examine/ExamineMyList.vue')), 'examinemylist')
const ExamineHandle = r => require.ensure([], () => r(require('../components/office/examine/ExamineHandle.vue')), 'examinehandle')
const ExamineView = r => require.ensure([], () => r(require('../components/office/examine/ExamineView.vue')), 'examineview')
const ExamineOpinion = r => require.ensure([], () => r(require('../components/office/examine/ExamineOpinion.vue')), 'examineopinion')
//任务
const Project = r => require.ensure([], () => r(require('../components/office/task/Project.vue')), 'project')
const TaskList = r => require.ensure([], () => r(require('../components/office/task/TaskList.vue')), 'tasklist')
const TaskView = r => require.ensure([], () => r(require('../components/office/task/TaskView.vue')), 'taskview')
const TaskHandle = r => require.ensure([], () => r(require('../components/office/task/TaskHandle.vue')), 'taskhandle')
const TaskSearch = r => require.ensure([], () => r(require('../components/office/task/TaskSearch.vue')), 'tasksearch')

//我板块
const MyPage = r => require.ensure([], () => r(require('../components/my/page/MyPage.vue')), 'my')
const SetPage = r => require.ensure([], () => r(require('../components/my/page/SetPage.vue')), 'set')
const AboutPage = r => require.ensure([], () => r(require('../components/my/page/AboutPage.vue')), 'about')
const UserPage = r => require.ensure([], () => r(require('../components/my/page/UserPage.vue')), 'user')

const Welcome = r => require.ensure([], () => r(require('../components/Welcome.vue')), 'welcome')
import Login from '@/components/Login'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/welcome/:action', //default  login
      name: 'welcome',
      component: Welcome
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    //动态模块
    {
      path: '/dynamic',
      name: 'dynamic',
      component: Dynamic,
      meta: { requiresAuth: true }
    },
    {
      path: '/littleassistant/:type', //type 1 助手  2 消息提醒
      name: 'littleassistant',
      component: LittleAssistant,
      meta: { requiresAuth: true }
    },
    //消息模块
    {
      path: '/news',
      name: 'news',
      component: News,
      meta: { requiresAuth: true }
    },
    {
      path: '/notice',
      name: 'notice',
      component: CompanyNotice,
      meta: { requiresAuth: true }
    },
    {
      path: '/articledetails/:id',
      name: 'articledetails',
      component: ArticleDetails,
      meta: { requiresAuth: true }
    },
    {
      path: '/list/:type/:action', //action  default 默认 select 选择
      name: 'list',
      component: List,
      meta: { requiresAuth: true }
    },
    {
      path: '/relatelist/:type/:fromtype/:action/:section/:row/:addition', //action  default 默认 select productmultiple(产品多选) 选择 section row 记录跳转位置(-1 表示无) addition可以是id
      name: 'relatelist',
      component: List,
      meta: { requiresAuth: true }
    },
    {
      path: '/adddata/:type/:action/:id', //action : default edit custombusiness（客户下添加商机操作） transform(线索转换变为直接一个请求触发)
      name: 'adddata',
      component: AddData,
      meta: { requiresAuth: true }
    },
    {
      path: '/maindetail/:id/:type',
      name: 'maindetail',
      component: CRMMainDetail,
      meta: { requiresAuth: true }
    },
    {
      path: '/crmdetail/:id/:type',
      name: 'crmdetail',
      component: CRMDetail,
      meta: { requiresAuth: true }
    },
    {
      path: '/addrelate/:id/:mainid/:type/:addtype', //type CRM类型 addtype添加类型 mainid 主详情下的id（回款单需要）
      name: 'addrelate',
      component: AddRelate,
      meta: { requiresAuth: true }
    },
    {
      path: '/selectlist/:id/:type/:selecttype/:section/:row', //type CRM类型 selecttype 展示什么数据用
      name: 'selectlist',
      component: SelectList,
      meta: { requiresAuth: true }
    },
    {
      path: '/moneyhistory/:id/:mainid', //回款历史 id 应收款ID  mainid 主详情下的id（回款单需要）
      name: 'moneyhistory',
      component: MoneyHistory,
      meta: { requiresAuth: true }
    },
    {
      path: '/filepreview', //文件url 用vuex传递
      name: 'filepreview',
      component: FilePreview,
      meta: { requiresAuth: true }
    },
    {
      path: '/relevanceaproduct', //关联产品页面
      name: 'relevanceaproduct',
      component: RelevanceAProduct,
      meta: { requiresAuth: true }
    },
    {
      path: '/moresort', //CRM更多排序页面
      name: 'moresort',
      component: MoreSort,
      meta: { requiresAuth: true }
    },
    //财务
    {
      path: '/financemain', //财务主页面
      name: 'financemain',
      component: FinanceMain,
      meta: { requiresAuth: true }
    },
    {
      path: '/financeadd/:type/:action/:id', //财务添加页面 default edit
      name: 'financeadd',
      component: FinanceAdd,
      meta: { requiresAuth: true }
    },
    {
      path: '/financeopinion/:type/:action/:id',// idID action 1同意 2拒绝 type 类型
      name: 'financeopinion',
      component: FinanceOpinion,
      meta: { requiresAuth: true }
    },
    {
      path: '/',
      name: 'crm',
      component: CRM,
      meta: { requiresAuth: true }
    },
    {
      path: '/crmsearch/:type/:action', //type CRM类型 action  default 默认 select 选择
      name: 'crmsearch',
      component: CRMSearch,
      meta: { requiresAuth: true }
    },
    {
      path: '/relatecrmsearch/:type/:action/:section/:row/:addition', //action  default 默认 select 选择 section row 记录跳转位置 addition可以是id
      name: 'relatecrmsearch',
      component: CRMSearch,
      meta: { requiresAuth: true }
    },
    {
      path: '/user/:id',
      name: 'user',
      component: UserPage,
      meta: { requiresAuth: true }
    },
    {
      path: '/fieldsign/:action',
      name: 'fieldsign',
      component: FieldSign,
      meta: { requiresAuth: true }
    },
    {
      path: '/teamsign',
      name: 'teamsign',
      component: TeamSign,
      meta: { requiresAuth: true }
    },
    {
      path: '/createsign',
      name: 'createsign',
      component: CreateSign,
      meta: { requiresAuth: true }
    },
    {
      path: '/signhistory',
      name: 'signhistory',
      component: SignHistory,
      meta: { requiresAuth: true }
    },
    {
      path: '/mapchoice',
      name: 'mapchoice',
      component: MapChoice,
      meta: { requiresAuth: true }
    },
    {
      path: '/rangechoice',
      name: 'rangechoice',
      component: RangeChoice,
      meta: { requiresAuth: true }
    },
    {
      path: '/officework',
      name: 'officework',
      component: OfficeWork,
      meta: { requiresAuth: true }
    },
    {
      path: '/checkstatistics',
      name: 'checkstatistics',
      component: CheckStatistics,
      meta: { requiresAuth: true }
    },
    {
      path: '/checkmonth/:time',//打卡月历 time 格式化时间
      name: 'checkmonth',
      component: CheckMonth,
      meta: { requiresAuth: true }
    },
    {
      path: '/checkset',//考勤设置
      name: 'checkset',
      component: CheckSet,
      meta: { requiresAuth: true }
    },
    {
      path: '/examinemain',
      name: 'examinemain',
      component: ExamineMain,
      meta: { requiresAuth: true }
    },
    {
      path: '/examinelist',
      name: 'examinelist',
      component: ExamineList,
      meta: { requiresAuth: true }
    },
    {
      path: '/examinemylist',
      name: 'examinemylist',
      component: ExamineMyList,
      meta: { requiresAuth: true }
    },
    {
      path: '/examinehandle/:type/:action/:id', //type 1 普通  2 请假 3 普通报销单 4 差旅报销单 5 出差申请单 6 借款申请单 action add edit
      name: 'examinehandle',
      component: ExamineHandle,
      meta: { requiresAuth: true }
    },
    {
      path: '/examineview/:id', //
      name: 'examineview',
      component: ExamineView,
      meta: { requiresAuth: true }
    },
    {
      path: '/examineopinion/:id/:action/:type', // id 审批ID action 1同意 2拒绝 type 审批类型
      name: 'examineopinion',
      component: ExamineOpinion,
      meta: { requiresAuth: true }
    },
    {
      path: '/schedule',
      name: 'schedule',
      component: Schedule,
      meta: { requiresAuth: true }
    },
    {
      path: '/createschedule/:action', //action  default edit
      name: 'createschedule',
      component: CreateSchedule,
      meta: { requiresAuth: true }
    },
    {
      path: '/conmmondetail/:type', //不同类型模块区分
      name: 'conmmondetail',
      component: CommonDetail,
      meta: { requiresAuth: true }
    },
    {
      path: '/tasklist/:id/:action', //action  default 默认 select 选择
      name: 'tasklist',
      component: TaskList,
      meta: { requiresAuth: true }
    },
    {
      path: '/taskview/:id',
      name: 'taskview',
      component: TaskView,
      meta: { requiresAuth: true }
    },
    {
      path: '/taskhandle/:id/:action', //action  default edit
      name: 'taskhandle',
      component: TaskHandle,
      meta: { requiresAuth: true }
    },
    {
      path: '/tasksearch/:type/:typeid/:action', //project tasklist
      name: 'tasksearch',
      component: TaskSearch,
      meta: { requiresAuth: true }
    },
    {
      path: '/project/:action', //action  default 默认 select 选择
      name: 'project',
      component: Project,
      meta: { requiresAuth: true }
    },
    //办公板块
    {
      path: '/office',
      name: 'office',
      component: Office,
      meta: { requiresAuth: true }
    },
    {
      path: '/loglist',
      name: 'loglist',
      component: LogList,
      meta: { requiresAuth: true }
    },
    {
      path: '/logdetail/:id', //id 日志ID
      name: 'logdetail',
      component: LogDetail,
      meta: { requiresAuth: true }
    },
    {
      path: '/createlog/:type/:action', //type 2月报  3周报  4日报 action add edit
      name: 'createlog',
      component: CreateLog,
      meta: { requiresAuth: true }
    },
    {
      path: '/createcomment/:type/:id/:receiveid/:name', //评论人id 和名字  type defalut（回复日志） comment（回复其他人） 任务下 taskdefalut（回复日志） taskcomment（回复其他人）
      name: 'createcomment',
      component: CreateComment,
      meta: { requiresAuth: true }
    },
    {
      path: '/addresslist',
      name: 'addresslist',
      component: AddressList,
      meta: { requiresAuth: true }
    },
    //我板块
    {
      path: '/my',
      name: 'my',
      component: MyPage,
      meta: { requiresAuth: true }
    },
    {
      path: '/set',
      name: 'set',
      component: SetPage,
      meta: { requiresAuth: true }
    },
    {
      path: '/about',
      name: 'about',
      component: AboutPage
    }
  ]
})



