
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import Value2nameFilter from 'vux/src/filters/value2name.js'

import { mapState } from 'vuex'
import $ from 'jquery'
import event from '../../../js/Event.js'
import { AjaxHttp } from '../../../js/ajaxHttp'
import PlateType from '../../../js/enum.js'
import { isArrayType, objToString, isPostNullValue, isNullValue, getYMDHMFormateTimestamp, getYMDFormateTimestamp, getYMDHMFormateTime, getYMDFormateTime, copyArr, getModuleKeyword } from "../../../js/utility.js"

export default {
components: {
XTextarea,
XAddress,
ChinaAddressV3Data,
Selector,
XInput,
Group,
Datetime,
Checker,
CheckerItem
},
data() {
return {
addressData: ChinaAddressV3Data,
selectorItem: null, //点击Selector 获取到的item 主要用于商机
}
},
created() {},
beforeDestroy() {
event.$off("saveDone");
},
mounted() {
if (this.addCustomInfos.length != 0) {
this.$nextTick(function() {
$('#scroll_area').scrollTop(this.addCustomInfosScroll); 
})
} else {
if (this.$route.params.action === 'default' || this.$route.params.action === 'custombusiness' || this.$route.params.action === 'edit') {
this.getfields();
}
}
event.$on('saveDone', function() {
this.saveInfos();
}.bind(this));
},
computed: {
...mapState([
'userInfo',
'addCustomInfos',
'addCustomInfosScroll',
'crmShowDetail', //详情用于生成编辑数据
'detail',
'crmListInfo',
'crmDetailInfo', //动态详情 编辑成功需要标记为刷新
])
},
beforeRouteLeave(to, from, next) {

if (to.name === 'selectlist') { //跳转选择列表(负责人)
event.$on('selectedRelateItem', function(data) {
this.handleSelectedInfo(data);
}.bind(this));
this.$store.commit('updateAddCustomInfosScroll', $('#scroll_area').scrollTop());
} else if (to.name === 'relatelist' && from.name === 'adddata') {
event.$on('selectedRelateItem', function(data) {
this.handleRelateInfo(data);
}.bind(this));
this.$store.commit('updateAddCustomInfosScroll', $('#scroll_area').scrollTop());
} else if ((to.name === 'list' && from.name === 'adddata') || (to.name === 'crmdetail' && from.name === 'adddata')) { //离开页面返回上层，需要重置数据 //添加页面返回普通列表  编辑页面
this.$store.commit('updateAddCustomInfos', []);
this.$store.commit('updateAddCustomInfosScroll', 0);
}
next();
},
beforeRouteEnter(to, from, next) {
if (from.name === 'selectlist' || from.name === 'relatelist') {
event.$off("selectedRelateItem");
}
next();
},
methods: {
getfields() { //获取添加字段
var params = {};
if (this.$route.params.type === PlateType.Leads) {
params['module'] = "leads";
} else if (this.$route.params.type === PlateType.Customer || this.$route.params.type === PlateType.CustomerPool) {
params['module'] = "customer";
} else if (this.$route.params.type === PlateType.Business) {
params['module'] = "business";
} else if (this.$route.params.type === PlateType.Contacts) {
params['module'] = "contacts";
} else if (this.$route.params.type === PlateType.Product) {
params['module'] = "product";
} else if (this.$route.params.type === PlateType.Contract) {
params['module'] = "contract";
}

if ((this.$route.params.type === PlateType.Customer || this.$route.params.type === PlateType.CustomerPool || this.$route.params.type === PlateType.Business) && this.$route.params.action === 'edit') {
params['action'] = "edit";
params['module_id'] = this.$route.params.id;
} else {
params['action'] = "add";
}

AjaxHttp('POST', 'm=index&a=fields', { params: JSON.stringify(params) }, this, function(res, wself, success) {
if (success && isArrayType(res.data)) {
if (wself.$route.params.action === 'default' || wself.$route.params.action === 'custombusiness') {
wself.handleAddType(res.data);
} else if (wself.$route.params.action === 'edit') {
wself.handleEditType(res.data);
}
}
});
},
handleEditType(list) {
for (var i = 0; i < list.length; i++) {
var bigItem = list[i];
bigItem['content'] = bigItem.default_value;
bigItem['fieldvalue'] = bigItem.default_value;
if (bigItem.form_type === 'address') {
bigItem['fieldvalue'] = [];
bigItem['addition'] = ''; 
} else if (bigItem.form_type === 'user') {
bigItem['content'] = this.userInfo.name;
bigItem['fieldvalue'] = this.userInfo.role_id;
} else if (this.$route.params.type === PlateType.Business || this.$route.params.type === PlateType.Contract) { //处理商机和合同下的产品信息
if (bigItem.form_type === 'product') {
var product_list = this.crmShowDetail.resData.product_info.product_list;
if (isArrayType(product_list)) {
var new_product_list = [];
for (var p = 0; p < product_list.length; p++) {
var item = product_list[p];
var newItem = {};
newItem.name = item.product_name;
newItem.id = item.product_id;
if (this.$route.params.type === PlateType.Business) {
newItem.r_id = item.r_id;
} else if (this.$route.params.type === PlateType.Contract) {
newItem.sales_product_id = item.sales_product_id;
}


newItem.standard = item.unit;
newItem.suggested_price = parseFloat(item.ori_price);
newItem.rate = item.discount_rate;
newItem.sell_price = parseFloat(item.unit_price);
newItem.count = item.amount;
newItem.subtotal = parseFloat(item.subtotal);
newItem.selected = true;

new_product_list.push(newItem);
}
bigItem.hasContent = true;
bigItem.itemlist = new_product_list;
bigItem.totalPrice = this.crmShowDetail.resData.product_info.total_subtotal_val;
bigItem.totalRate = this.crmShowDetail.resData.product_info.final_discount_rate;
bigItem.totalAmount = this.crmShowDetail.resData.product_info.total_amount;
bigItem.sellTotalPrice = this.crmShowDetail.resData.product_info.final_price;
}
}
}

for (var j = 0; j < this.crmShowDetail.resData.data.length; j++) {
var littleItem = this.crmShowDetail.resData.data[j];
if (bigItem.field === littleItem.field) {

if (littleItem.form_type === 'checkbox') {
if (bigItem.fieldvalue !== '') { bigItem.fieldvalue = littleItem.val.split("\n"); }
} else if (littleItem.form_type === 'select') {
bigItem.content = littleItem.val;
bigItem.fieldvalue = littleItem.val;
} else if (littleItem.form_type === 'datetime') {
if (this.$route.params.type === PlateType.Contract) {
bigItem.form_addition = "YYYY-MM-DD";
bigItem.fieldvalue = getYMDFormateTime(littleItem.val);
} else {
bigItem.form_addition = "YYYY-MM-DD HH:mm";
bigItem.fieldvalue = getYMDHMFormateTime(littleItem.val);
}

}







else if (littleItem.form_type === 'address') {
if (bigItem.fieldvalue !== '') {
var array = littleItem.val.split(" ");
if (array.length > 3) {
bigItem.fieldvalue = array.slice(0, 3);
bigItem.addition = array[3];
} else {
bigItem.fieldvalue = array;
}
}
bigItem.rawvalue = true;
} else {
bigItem.content = littleItem.val;
bigItem.fieldvalue = littleItem.id;
}
}
}


}

var mainList = [];
var additionList = [];
var contactsList = [];

var customerIndex = -1; 
var contactsItem = -1;

var contactsIndex = -1; 
var customerItem = -1;
for (var i = 0; i < list.length; i++) {
var item = list[i];
if (item.is_main === '1' || item.is_main === 1) {
if (this.$route.params.type === PlateType.Business) { //商机下读取客户联系人信息
if (item.form_type === 'customer') {
customerIndex = mainList.length;
customerItem = item;
} else if (item.form_type === 'contacts') {
contactsIndex = mainList.length;
contactsItem = item;
}
} else if (this.$route.params.type === PlateType.Contract) { //合同下读取客户商机信息
if (item.form_type === 'customer') {
customerIndex = mainList.length;
customerItem = item;
} else if (item.form_type === 'business') {
contactsIndex = mainList.length;
contactsItem = item;
}
}

mainList.push(item);
} else if (item.is_main === '2' || item.is_main === 2) {
contactsList.push(item);
} else {
additionList.push(item);
}
}

if (this.$route.params.type === PlateType.Business || this.$route.params.type === PlateType.Contract) {
contactsItem.addition = customerIndex; 
customerItem.addition = contactsIndex; 
}

var totalList = [];
totalList.push(mainList);
if (this.$route.params.type === PlateType.Customer || this.$route.params.type === PlateType.CustomerPool) {
if (contactsList.length > 0) {
totalList.push(contactsList);
}
}

if (additionList.length > 0) {
totalList.push(additionList);
}

this.$store.commit('updateAddCustomInfos', totalList);
},
handleAddType(list) {
var mainList = [];
var additionList = [];
var contactsList = [];

var customerIndex = -1; 
var contactsItem = -1;

var contactsIndex = -1; 
var customerItem = -1;
for (var i = 0; i < list.length; i++) {
var item = list[i];
item['content'] = item.default_value;
item['fieldvalue'] = item.default_value;
if (item.form_type === 'address') {
item['fieldvalue'] = [];
item['addition'] = ''; 
item.rawvalue = true;
} else if (item.form_type === 'user') {
item['content'] = this.userInfo.name;
item['fieldvalue'] = this.userInfo.role_id;
} else if (item.form_type === 'datetime') { //添加的时候 因为合同选择不包含具体时间所以加入 form_addition 属性 辅助控制
if (this.$route.params.type === PlateType.Contract) {
item['form_addition'] = "YYYY-MM-DD";
} else {
item['form_addition'] = "YYYY-MM-DD HH:mm";
}
}

if (this.$route.params.action === 'custombusiness' && item.form_type === 'customer') { //客户下的添加商机
if (this.detail.type === 'custombusiness') {
item['content'] = this.detail.name;
item['fieldvalue'] = this.detail.id;
}
}


if (item.is_main === '1' || item.is_main === 1) {
if (this.$route.params.type === PlateType.Business) { //商机下读取客户联系人信息
if (item.form_type === 'customer') {
customerIndex = mainList.length;
customerItem = item;
} else if (item.form_type === 'contacts') {
contactsIndex = mainList.length;
contactsItem = item;
}
} else if (this.$route.params.type === PlateType.Contract) { //合同下读取客户商机信息
if (item.form_type === 'customer') {
customerIndex = mainList.length;
customerItem = item;
} else if (item.form_type === 'business') {
contactsIndex = mainList.length;
contactsItem = item;
}
}

mainList.push(item);
} else if (item.is_main === '2' || item.is_main === 2) {
contactsList.push(item);
} else {
additionList.push(item);
}
}

if (this.$route.params.type === PlateType.Business || this.$route.params.type === PlateType.Contract) {
contactsItem.addition = customerIndex; 
customerItem.addition = contactsIndex; 
}

var totalList = [];
totalList.push(mainList);
if (this.$route.params.type === PlateType.Customer || this.$route.params.type === PlateType.CustomerPool) {
if (contactsList.length > 0) {
totalList.push(contactsList);
}
}

if (additionList.length > 0) {
totalList.push(additionList);
}

this.$store.commit('updateAddCustomInfos', totalList);
},
saveInfos() {

var add = true;
var postParams = {};

for (var j = 0; j < this.addCustomInfos.length; j++) {
var bigItem = this.addCustomInfos[j];
for (var i = 0; i < bigItem.length; i++) {
var item = bigItem[i];
var fieldValue = '';
if (item.form_type === 'text' || item.form_type === 'number' || item.form_type === 'floatnumber' || item.form_type === 'email' || item.form_type === 'mobile' || item.form_type === 'textarea') {
fieldValue = item.content;
} else {
fieldValue = item.fieldvalue;
}

if (item.is_validate === '1' && item.is_null === '1' && isPostNullValue(fieldValue)) {
this.$vux.toast.text(item.name + '是必填项', 'top');
add = false;
break;
}

if (item.form_type === 'mobile' && !isNullValue(fieldValue)) {
var myreg = /^1[34587][0-9]{9}?$/;
if (!myreg.test(fieldValue)) {
this.$vux.toast.text('请输入有效的手机号码', 'top');
add = false;
break;
}
} else if (item.form_type === 'email' && !isNullValue(fieldValue)) {
var myreg = /^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)/;
if (!myreg.test(fieldValue)) {
this.$vux.toast.text('请输入有效的邮箱', 'top');
add = false;
break;
}
}


if (item.form_type === 'datetime' && !isPostNullValue(fieldValue)) {
if (this.$route.params.type === PlateType.Contract) {
fieldValue = getYMDFormateTimestamp(fieldValue);
} else {
fieldValue = getYMDHMFormateTimestamp(fieldValue);
}

} else if (item.form_type === 'address') {
var addresstempvalue = Value2nameFilter(fieldValue, ChinaAddressV3Data);
var addressfieldvalue = addresstempvalue === '' ? [] : addresstempvalue.split(" ");
if (isPostNullValue(addressfieldvalue)) {
addressfieldvalue = addressfieldvalue.concat(['', '', '']);
}
addressfieldvalue.push(isPostNullValue(item.addition) ? '' : item.addition);
fieldValue = JSON.stringify(addressfieldvalue);
} else if (item.form_type === 'checkbox') {
fieldValue = JSON.stringify(fieldValue);
} else if (item.form_type === 'product') { //商机下的产品关联
if (item.hasContent === true) {
var products = [];
for (var k = 0; k < item.itemlist.length; k++) {
var productItem = item.itemlist[k];
if (productItem.sales_product_id || productItem.r_id) {
if (this.$route.params.type === PlateType.Business) {
products.push({
'r_id': productItem.r_id,
'product_id': productItem.id,
'ori_price': productItem.suggested_price,
'discount_rate': productItem.rate,
'unit_price': productItem.sell_price,
'amount': productItem.count,
'unit': productItem.standard,
'subtotal': productItem.count * productItem.sell_price
});
} else if (this.$route.params.type === PlateType.Contract) {
products.push({
'sales_product_id': productItem.sales_product_id,
'product_id': productItem.id,
'ori_price': productItem.suggested_price,
'discount_rate': productItem.rate,
'unit_price': productItem.sell_price,
'amount': productItem.count,
'unit': productItem.standard,
'subtotal': productItem.count * productItem.sell_price
});
}

} else {
products.push({
'product_id': productItem.id,
'ori_price': productItem.suggested_price,
'discount_rate': productItem.rate,
'unit_price': productItem.sell_price,
'amount': productItem.count,
'unit': productItem.standard,
'subtotal': productItem.count * productItem.sell_price
});
}

}
postParams['product'] = products;

postParams['total_subtotal_val'] = item.totalPrice;
postParams['final_discount_rate'] = item.totalRate;
postParams['final_price'] = item.sellTotalPrice;
postParams['total_amount'] = item.totalAmount;
}
}

if (item.field !== 'product[]') {
postParams[item.field] = fieldValue;
}
}
}

if (add) {
var url = '';
if (this.$route.params.action === 'default' || this.$route.params.action === 'custombusiness') {
url = "m=" + getModuleKeyword(this.$route.params.type) + "&a=add";
} else if (this.$route.params.action === 'edit') {
url = "m=" + getModuleKeyword(this.$route.params.type) + "&a=edit";
postParams['id'] = this.$route.params.id;
}

AjaxHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
if (wself.$route.params.action === 'custombusiness') { //客户下添加商机 返回要刷新列表
var name = wself.$route.params.type + "@" + wself.$route.params.id;
var cacheItem = wself.crmDetailInfo[name];
cacheItem.refresh = true;
wself.$store.commit('updateCrmDetailInfo', wself.crmDetailInfo);
}
if (wself.$route.params.action === 'edit') { //编辑完成刷新详情信息 防止重新编辑出错
wself.crmShowDetail.refresh = true;
wself.$store.commit('updateCrmShowDetail', wself.crmShowDetail);
var name = wself.$route.params.type + "@" + wself.$route.params.id;
var cacheItem = wself.crmDetailInfo[name];
cacheItem.refreshTop = true;
cacheItem.refresh = true;
wself.$store.commit('updateCrmDetailInfo', wself.crmDetailInfo); 
}

if (wself.$route.params.action === 'default') { //如果是添加页面
wself.crmListInfo.topRefresh = true;
wself.$store.commit('updateCrmListInfo', wself.crmListInfo);
}
wself.$vux.toast.text(res.info, 'top');
wself.$router.go(-1);
}
});
}

},
cellTitle(index) {
if (index === 0) {
return '基本信息';
} else if (index === 1) {
if ((this.$route.params.type === PlateType.Customer || this.$route.params.type === PlateType.CustomerPool) && this.addCustomInfos.length === 3) {
return '首要联系人信息';
} else {
return '附加信息';
}
} else {
return '附加信息';
}

},
onRelateClick(item, section, row) { //相关跳转列表

if (item.form_type === 'product') {
this.$router.push({
name: 'relatelist',
params: {
type: PlateType.Product,
fromtype: this.$route.params.type,
action: 'productmultiple',
section: section.toString(),
row: row.toString(),
addition: '0'
}
});
} else {


var type = '';
if (item.form_type === 'customer') { //客户
type = PlateType.Customer;
} else if (item.form_type === 'contacts') { //联系人
type = PlateType.Contacts;
} else if (item.form_type === 'user') { //负责人 暂无
type = '';
} else if (item.form_type === 'business') { //商机列表
type = PlateType.Business;
}

if (this.$route.params.type === PlateType.Business && item.form_type === 'contacts') {
if (item.addition >= 0) {
var customerItem = this.addCustomInfos[0][item.addition];
if (isNullValue(customerItem.fieldvalue)) {
this.$vux.toast.text('请先选择客户', 'top');
} else {
this.$router.push({
name: 'relatelist',
params: {
type: type,
fromtype: this.$route.params.type,
action: 'select',
section: section.toString(),
row: row.toString(),
addition: customerItem.fieldvalue
}
});
}
}
} else if (this.$route.params.type === PlateType.Contract && item.form_type === 'business') {
if (item.addition >= 0) {
var customerItem = this.addCustomInfos[0][item.addition];
if (isNullValue(customerItem.fieldvalue)) {
this.$vux.toast.text('请先选择客户', 'top');
} else {
this.$router.push({
name: 'relatelist',
params: {
type: type,
fromtype: this.$route.params.type,
action: 'select',
section: section.toString(),
row: row.toString(),
addition: { customer_id: customerItem.fieldvalue }
}
});
}
}
} else if (item.form_type === 'user') {
this.$router.push({
name: 'selectlist',
params: {
id: '-1',
type: this.$route.params.type,
selecttype: '2', //负责人
section: section.toString(),
row: row.toString(),
}
});
} else {
this.$router.push({
name: 'relatelist',
params: {
type: type,
fromtype: this.$route.params.type,
action: 'select',
section: section.toString(),
row: row.toString(),
addition: '0'
}
});
}
}
},
handleRelateInfo(data) {
var section = parseInt(data.section);
var row = parseInt(data.row);
var item = this.addCustomInfos[section][row];
item.content = data.item.title;
item.fieldvalue = data.item.id;
this.$store.commit('updateCustomInfoItem', {
item: item,
section: section,
row: row
});


if ((data.fromtype === PlateType.Business || data.fromtype === PlateType.Contract) && data.type === PlateType.Customer) {
if (item.addition) {
var additionitem = this.addCustomInfos[0][parseInt(item.addition)];
additionitem.content = '';
additionitem.fieldvalue = '';
this.$store.commit('updateCustomInfoItem', {
item: additionitem,
section: 0,
row: item.addition
});
}
}
},
handleSelectedInfo(data) {
var section = parseInt(data.section);
var row = parseInt(data.row);
var item = this.addCustomInfos[section][row];
item.content = data.item.user_name;
item.fieldvalue = data.item.role_id;
},
onSelectorChange(val) { //select change 主要用于商机
if (this.$route.params.type === PlateType.Business) {
if (this.selectorItem && this.selectorItem.field === "status_type_id") { //商机下点击商机状态组
AjaxHttp('POST', 'm=business&a=getbusinessStatus', { 'status_type_id': val }, this, function(res, wself, success) {
if (success && isArrayType(res.data)) {
var temps = [];
for (var i = 0; i < res.data.length; i++) {
var item = res.data[i];
temps.push({ key: item.status_id, value: item.name });
}
for (var j = 0; j < wself.addCustomInfos.length; j++) {
var bigItem = wself.addCustomInfos[j];
for (var i = 0; i < bigItem.length; i++) {
var item = bigItem[i];
if (item.field === 'status_id') {
wself.selectorItem = null; 
item.fieldvalue = '';
item.setting = temps;
}
}
}
}
});
}
}
},
onSelectorClick(item) {
if (this.$route.params.type === PlateType.Business) {
this.selectorItem = item;
}
},
onShadowChange (ids, names) {
for (var j = 0; j < this.addCustomInfos.length; j++) {
var bigItem = this.addCustomInfos[j];
for (var i = 0; i < bigItem.length; i++) {
var item = bigItem[i];
if (item.form_type === 'address' && item.rawvalue) {
item.rawvalue = false;
}
}
}
}
}
}




