import { getYMDFormateTime, isArrayType } from "../../../js/utility.js"

export default {
  name: 'freceivingorder-cell',
  components: {},
  data() {
    return {}
  },
  mounted() {

  },
  computed: {
    pay_time() {
      return getYMDFormateTime(this.item.pay_time);
    },
    statusColor() {
      if (this.item.status === '0') {
        return '#F5CA00';
      } else if (this.item.status === '1') {
        return '#7CCA4E';
      } else if (this.item.status === '2') {
        return '#F5715F';
      }
      return '#FFFFFF';
    },
    status() {
      if (this.item.status === '0') {
        return '待审';
      } else if (this.item.status === '1') {
        return '通过';
      } else if (this.item.status === '2') {
        return '拒绝';
      }
      return '';
    }
  },
  props: {
    item: {
      "name": "",
      "pay_time": "",
      "money": "",
      "status": '',
      "id": '',
    }
  },
  methods: {}
}




