
import md5 from 'js-md5'
import { AjaxHttp, AjaxFullHttp } from '../js/ajaxHttp'
import { getImageUrl, getTimestamp } from "../js/utility.js"
import { mapState } from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import CryptoJS from 'crypto-js'
import { PLATFORM } from '../config.js'

export default {
data() {
return {
name: '',
password: '',
canuse: true,
num_id: ''
}
},
components: {
Group,
XInput
},
computed: {
...mapState([
'userInfo',
'welcomeInfo'
])
},
mounted() {

this.num_id = window.localStorage.wknum_id;
if (this.userInfo !== '' && typeof this.userInfo.token !== 'undefined' && typeof window.localStorage.wkurl !== 'undefined') {
this.$router.replace({
path: '/'
});
}
},
methods: {
login() {


this.canuse = false;
AjaxHttp('POST', 'm=user&a=login', {
name: this.name,
password: md5(this.password),
num_id: this.num_id
}, this, function(res, wself, success) {
wself.canuse = true;
if (success) {
res.img = getImageUrl(res.img); 
wself.getPermission(res);
let user = res;

document.title = res.system_name;
if (PLATFORM !== 'web' && window.xgpush) {
window.xgpush.registerPush(wself.num_id + '@' + res.role_id).then(function(results) {


}); //注册信鸽推送
}

wself.$store.dispatch('setUserInfo', user);
window.sessionStorage.user = JSON.stringify(user);
window.localStorage.user = JSON.stringify(user);
window.localStorage.wknum_id = wself.num_id;
window.localStorage.system_name = res.system_name;

wself.welcomeInfo.login = true;
wself.$store.commit('updateWelcomeInfo', wself.welcomeInfo);
wself.$router.go(-1);



}
});
},
getPermission(res) {
var temps = ['customer', 'business', 'knowledge', 'contacts', 'product', 'leads', 'contract', 'announcement', 'examine', 'event', 'sign', 'task', 'address_book', 'finance'];
var tempsFinance = ["receivables", "receivingorder", "payables", "paymentorder"];
if (res.admin === 1) {
for (var i = 0; i < temps.length; i++) {
res[temps[i]] = true;
}
for (var i = 0; i < tempsFinance.length; i++) {
res[tempsFinance[i]] = true;
}
} else {
if (res.permission) {
for (var j = 0; j < res.permission.length; j++) {
res[res.permission[j]] = true;
}
}
if (res.finance_type) {
for (var j = 0; j < res.finance_type.length; j++) {
res[res.finance_type[j]] = true;
}
}
}

},
getNumName() {
if (this.num_id.length === 0) {
this.$vux.toast.text('请输入组织名', 'top');
return;
}
if (this.name.length === 0) {
this.$vux.toast.text('请输入用户名', 'top');
return;
}
if (this.password.length === 0) {
this.$vux.toast.text('请输入密码', 'top');
return;
}
this.canuse = false;
var sendtimestamp = getTimestamp(new Date());
var clientSign = CryptoJS.HmacSHA1('num_id=' + this.num_id + '&sendtimestamp=' + sendtimestamp, 'mxcrm@2015#sdSDF54fjH==').toString();
var wordArray = CryptoJS.enc.Utf8.parse(clientSign);
var base64 = CryptoJS.enc.Base64.stringify(wordArray);



AjaxFullHttp('POST', 'http://upgrade.crm.com/index.php?m=Index&a=checkServernew', {
'num_id': this.num_id,
'sendtimestamp': sendtimestamp,
'clientSign': base64,
}, this, function(res, wself, success) {
wself.canuse = true;
if (success) {








window.localStorage.wkurl = res.data.url;
wself.login();
}
});
},
num_idBlur(value, $event) {
window.localStorage.wknum_id = value;
}
}
}




