import './dialog.scss';

export default {
  name: 'dialog',
  props:{
    message:String,
    confirmTxt:String,
    cancleTxt:String,
    type:String,
    title:String,
    isShow:Boolean,
    callback:null,
    cancleCallback:null
  },
  methods:{
    confirmEvent(){
      this.confirmCallback();
      document.body.removeChild(this.$el);
    },
    cancleEvent(){
      this.cancleCallback();
      document.body.removeChild(this.$el);
    }
  }
}



