import './switch.scss';

export default {
  name:"vswitch",
  data(){
    return {
      currentValue : this.value
    }
  },
  props:{
    disabled:{
      type:Boolean,
      default:false
    },
    value:{
      type:Boolean,
      default:false
    }
  },
  watch:{
    currentValue(){
      this.$emit('sChange', this.currentValue);
    }
  },
  methods:{
    
  }
};



