
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import Value2nameFilter from 'vux/src/filters/value2name.js'

import { mapState } from 'vuex'
import $ from 'jquery'
import event from '../../../js/Event.js'
import { AjaxHttp } from '../../../js/ajaxHttp'
import PlateType from '../../../js/enum.js'
import { getCommunicationAddInfo, getReceivablesAddInfo, getReceivingorderAddInfo } from '../data/relateData.js'
import { isArrayType, objToString, isPostNullValue, getYMDHMFormateTimestamp, getYMDFormateTimestamp, copyArr, getModuleKeyword } from "../../../js/utility.js"

export default {
name: 'addrelate',
components: {
XTextarea,
XAddress,
ChinaAddressV3Data,
Selector,
XInput,
Group,
Datetime,
Checker,
CheckerItem
},
data() {
return {
addressData: ChinaAddressV3Data,
detail: {}, //添加回款需要的信息
}
},
created() {},
beforeDestroy() {
event.$off("saveDone");
},
beforeRouteLeave(to, from, next) {

if (to.name === 'relatelist') {
event.$on('selectedRelateItem', function(data) {
this.handleRelatedInfo(data);
}.bind(this));
} else if (to.name === 'selectlist') {
event.$on('selectedRelateItem', function(data) {
this.handleSelectedInfo(data);
}.bind(this));
} else if (to.name === 'maindetail' || to.name === 'mainrelatedetail' || to.name === 'moneyhistory') { //离开页面返回上层，需要重置数据 //添加页面返回普通列表  编辑页面
this.$store.commit('updateAddRelateInfos', []);
}
next();
},
beforeRouteEnter(to, from, next) {
if (from.name === 'relatelist' || to.name === 'selectlist') {
event.$off("selectedRelateItem");
}
next();
},
mounted() {
if (this.addRelateInfos.length === 0) {
this.getAddInfo();
}
event.$on('saveDone', function() {
this.saveInfos();
}.bind(this));
},
computed: {
...mapState([
'userInfo',
'crmDetailInfo',
'addRelateInfos'
])
},

methods: {
getAddInfo() {
if (this.$route.params.addtype === '0') { //添加日志 当选数据源换成 data  时间格式信息  换成 data
this.getCommunicationType();
} else if (this.$route.params.addtype === '12') { //添加应收款
AjaxHttp('POST', 'm=finance&a=getCode', { //获取应收款编号
t: 'receivables',
id: this.$route.params.id
}, this, function(res, wself, success) {
if (success) {
var name = wself.$route.params.type + "@" + wself.$route.params.mainid;
var cacheItem = wself.crmDetailInfo[name];
wself.$store.commit('updateAddRelateInfos', getReceivablesAddInfo(res.data, cacheItem.detail, wself.userInfo));
}
});
} else if (this.$route.params.addtype === '5') { //添加回款
AjaxHttp('POST', 'm=finance&a=getCode', {
t: 'receivingorder',
id: this.$route.params.id
}, this, function(res, wself, success) {
if (success) {

var data = [];
for (var i = 0; i < res.list.length; i++) {
var item = res.list[i];

data.push({ key: item.account_id, value: item.open_bank });
}
wself.detail = res.list;
wself.$store.commit('updateAddRelateInfos', getReceivingorderAddInfo(data, wself.userInfo));
}
});
}
},
saveInfos() {
var add = true;
var postParams = {};

for (var i = 0; i < this.addRelateInfos.length; i++) {
var item = this.addRelateInfos[i];
var fieldValue = '';
if (item.form_type === 'text' || item.form_type === 'number' || item.form_type === 'floatnumber' || item.form_type === 'email' || item.form_type === 'mobile' || item.form_type === 'textarea') {
if (item.disabled) {
fieldValue = item.fieldvalue;
} else {
fieldValue = item.content;
}
} else {
fieldValue = item.fieldvalue;
}

if (item.is_null === '1' && isPostNullValue(fieldValue)) {
this.$vux.toast.text(item.name + '是必填项', 'top');
add = false;
break;
}
if (item.form_type === 'datetime' && !isPostNullValue(fieldValue)) {
if (item.data === 'YYYY-MM-DD HH:mm') {
fieldValue = getYMDHMFormateTimestamp(fieldValue);
} else if (item.data === 'YYYY-MM-DD') {
fieldValue = getYMDFormateTimestamp(fieldValue);
}
} else if (item.form_type === 'address') {
var addresstempvalue = Value2nameFilter(fieldValue, ChinaAddressV3Data);
var addressfieldvalue = addresstempvalue === '' ? [] : addresstempvalue.split(" ");
if (isPostNullValue(addressfieldvalue)) {
addressfieldvalue = addressfieldvalue.concat(['', '', '']);
}
addressfieldvalue.push(isPostNullValue(item.addition) ? '' : item.addition);
fieldValue = JSON.stringify(addressfieldvalue);
} else if (item.form_type === 'checkbox') {
fieldValue = JSON.stringify(fieldValue);
}
postParams[item.field] = fieldValue;

}


if (add) {
this.requestHandle(postParams);
}
},
requestHandle(postParams) {

var url = '';
if (this.$route.params.addtype === '0') { //添加日志
url = "m=log&a=add";
if (!isPostNullValue(postParams['id']) && (this.$route.params.type == PlateType.Customer || this.$route.params.type === PlateType.CustomerPool)) {
postParams['module'] = 'business';
} else {
postParams['id'] = this.$route.params.id;
postParams['module'] = getModuleKeyword(this.$route.params.type);
}
} else if (this.$route.params.addtype === '12') { //添加应收款
url = "m=finance&a=add";
postParams['t'] = 'receivables';
postParams['id'] = this.$route.params.id;
} else if (this.$route.params.addtype === '5') { //添加回款
url = "m=finance&a=add";
postParams['t'] = 'receivingorder';
postParams['receivables_id'] = this.$route.params.id;
}
AjaxHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
var name = wself.$route.params.type + "@" + wself.$route.params.mainid;
var cacheItem = wself.crmDetailInfo[name];
cacheItem.refresh = true;
wself.$store.commit('updateCrmDetailInfo', wself.crmDetailInfo);
wself.$vux.toast.text(res.info, 'top');
if (wself.$route.params.addtype === '5' && wself.$route.params.type === PlateType.Contract) { //合同下应收款里的回款单 回跳两次  应收款下的回款单 跳转一次
wself.$router.go(-2);
} else {
wself.$router.go(-1);
}
}
});
},

getCommunicationType() {
if (this.$route.params.type === PlateType.Customer || this.$route.params.type === PlateType.CustomerPool || this.$route.params.type === PlateType.Business) {
AjaxHttp('POST', 'm=log&a=logstatus', {}, this, function(res, wself, success) {
if (success) {
wself.$store.commit('updateAddRelateInfos', getCommunicationAddInfo(wself.$route.params.type, res.list));
}
});
} else {
this.$store.commit('updateAddRelateInfos', getCommunicationAddInfo(this.$route.params.type));
}
},
onRelateClick(item, row) { //相关跳转列表
if (item.form_type === 'business') { //商机
this.$router.push({
name: 'relatelist',
params: {
type: PlateType.Business,
fromtype: this.$route.params.type,
action: 'select',
section: '0',
row: row.toString(),
addition: { customer_id: this.$route.params.id }
}
});
} else if (item.form_type === 'user') {
this.$router.push({
name: 'selectlist',
params: {
id: '-1',
type: this.$route.params.type,
selecttype: '2',
section: '-1',
row: row.toString(),
}
});
}
},
handleRelatedInfo(data) { //相关列表数据处理
var row = parseInt(data.row);
var item = this.addRelateInfos[row];
item.content = data.item.title;
item.fieldvalue = data.item.id;
this.$store.commit('updateRelateInfoItem', {
item: item,
row: row
});
},
handleSelectedInfo(data) {
if (data.type === '2') { //负责人选择
var row = parseInt(data.row);
var item = this.addRelateInfos[row];
item.content = data.item.user_name;
item.fieldvalue = data.item.role_id;
this.$store.commit('updateRelateInfoItem', {
item: item,
row: row
});
}
},
onChange(val) {
if (this.$route.params.addtype === '5') {
for (var i = 0; i < this.detail.length; i++) {
var item = this.detail[i];
if (item.account_id === val) {
var accountItem = this.addRelateInfos[2];
accountItem.fieldvalue = item.bank_account;
accountItem.content = item.bank_account;
this.$set(this.addRelateInfos, 2, accountItem);
var companyItem = this.addRelateInfos[3];
companyItem.fieldvalue = item.company;
companyItem.content = item.company;
this.$set(this.addRelateInfos, 3, companyItem);
}
}
}
}
}
}




