import { getYMDFormateTime, isArrayType } from "../../../js/utility.js"

export default {
  name: 'freceivables-cell',
  components: {},
  data() {
    return {}
  },
  mounted() {

  },
  computed: {
    pay_time() {
      return getYMDFormateTime(this.item.pay_time);
    },
    count() {
      return this.item.receivingorder.length;
    }
  },
  props: {
    item: {
      "name": "",
      "pay_time": "",
      "price": "",
      "un_payable": "",
      "schedule": '',
      "id": ''
    }
  },
  methods: {}
}




