
import PlateType from '../../../js/enum.js'
import { AjaxHttp } from '../../../js/ajaxHttp'
import Color from '../../../js/color.js'
import ListCell from '../components/ListCell'
import event from '../../../js/Event.js'
import { getYMDHMSFormateTime, isArrayType, objToString, getModuleKeyword, copyObject, isNullValue } from "../../../js/utility.js"
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import { mapState } from 'vuex'
import { getReceivablesShowInfo, getReceivingorderShowInfo } from '../data/financeData.js'

export default {
name: 'crmdetail',
data() {
return {
type: '',
itemList: [],
showRightMenus: false,
rightMenus: {},
permission: false,
resData: {}, //服务器返回的未处理的信息  编辑时候用
}
},
components: {
ListCell,
Actionsheet,
},
computed: {
PlateType() {
return PlateType;
},
...mapState([
'crmShowDetail',
'crmListInfo' 
]),
},
mounted() {
event.$on('onClickMore', function(path) {
this.showRightMenus = true;
}.bind(this));

this.type = this.$route.params.type;

if (this.crmShowDetail.type === 'crmdetail' && (this.crmShowDetail.crmtype === this.type) && (this.crmShowDetail.crmid === this.$route.params.id) && this.crmShowDetail.itemList.length > 0 && !this.crmShowDetail.refresh) {
this.itemList = this.crmShowDetail.itemList;
this.rightMenus = this.crmShowDetail.rightMenus;
this.resData = this.crmShowDetail.resData;
this.permission = this.crmShowDetail.permission;
if (this.permission) {
this.$store.commit('updateXheaderRightOptions', true);
}
this.$nextTick(() => {
this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, this.crmShowDetail.scroll, false);
});
} else {
this.$refs.my_scroller.triggerPullToRefresh();
}
},
beforeRouteLeave(to, from, next) {
if (to.name === 'adddata' || to.name === 'maindetail' || to.name === 'user' || to.name === 'financeadd') {

this.crmShowDetail.type = 'crmdetail';
this.crmShowDetail.crmtype = this.$route.params.type;
this.crmShowDetail.crmid = this.$route.params.id;
this.crmShowDetail.itemList = this.itemList;
this.crmShowDetail.rightMenus = this.rightMenus;
this.crmShowDetail.resData = this.resData;
this.crmShowDetail.permission = this.permission;
this.crmShowDetail.refresh = false;
this.crmShowDetail.scroll = this.$refs.my_scroller.getPosition().top;
this.$store.commit('updateCrmShowDetail', this.crmShowDetail);
} else {
this.$store.commit('updateCrmShowDetail', {});
}
next();
},
beforeDestroy() {
event.$off("onClickMore");
},
methods: {
infinite(done) {
done(true);
},
refresh(done) {
if (this.type === PlateType.FinanceReceivables || this.type === PlateType.FinanceReceivingorder) {
this.getFinanceDetail(done);
} else {
this.getDetail(done);
}
},
getDetail(done) {
var url = 'm=' + getModuleKeyword(this.type) + '&a=view';
AjaxHttp('POST', url, {
'id': this.$route.params.id
}, this, function(res, wself, success) {
if (success) {
if (typeof done === 'function') {
done(true);
}

if (wself.type === PlateType.Product) {
wself.itemList = res.data;
} else {
var permission = res.permission;
if (permission.edit === 1 || permission.delete === 1) {
wself.$store.commit('updateXheaderRightOptions', true);
wself.permission = true;
if (permission.edit === 1) { //商机和合同的编辑 暂时未实现
wself.rightMenus.edit = '编辑';
}
if (permission.delete === 1) {
wself.rightMenus.delete = '删除';
}
} else {
wself.permission = false;
}

if (permission.view === 1) {
if (isArrayType(res.data)) {
wself.resData = res; 
wself.itemList = res.data;
}
}

}
}
});
},
getFinanceDetail(done) {
var url = '';
var postParams = {};
if (this.type === PlateType.FinanceReceivables) {
url = 'm=finance&a=view';
postParams['t'] = 'receivables';
} else if (this.type === PlateType.FinanceReceivingorder) {
url = 'm=finance&a=view';
postParams['t'] = 'receivingorder';
}
postParams['id'] = this.$route.params.id;
AjaxHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
if (typeof done === 'function') {
done(true);
}

var permission = res.permission;
if (permission.edit === 1 || permission.delete === 1) {
wself.$store.commit('updateXheaderRightOptions', true);
wself.permission = true;
if (permission.edit === 1) { //商机和合同的编辑 暂时未实现
wself.rightMenus.edit = '编辑';
}
if (permission.delete === 1) {
wself.rightMenus.delete = '删除';
}
} else {
wself.permission = false;
}

wself.resData = res; 
if (wself.type === PlateType.FinanceReceivables) {
wself.itemList = getReceivablesShowInfo(res.data);
} else if (wself.type === PlateType.FinanceReceivingorder) {
wself.itemList = getReceivingorderShowInfo(res.data);
}
}
});
},
getDetailVal(item) {
if (item.form_type === 'datetime') {
return getYMDHMSFormateTime(item.val);
}
return item.val;
},
getDetailColor(type) {
if (type === 0 || type === '0') {
return '#8D8D8D';
}
return Color.HeaderColor;
},
clickRightMenu(key) {
if (key == 'edit') {
if (this.type === PlateType.FinanceReceivables || this.type === PlateType.FinanceReceivingorder) {
this.$router.push({ name: 'financeadd', params: { type: this.type, action: 'edit', id: this.$route.params.id } });
} else {
this.$router.push({ name: 'adddata', params: { type: this.type, action: 'edit', id: this.$route.params.id } });
}
} else if (key == 'delete') {
this.deleteDetail();
}
},
deleteDetail() {
var url = 'm=' + getModuleKeyword(this.type) + '&a=delete';
var postParams = {};
postParams['id'] = this.$route.params.id;
if (this.type === PlateType.FinanceReceivables) {
postParams['t'] = 'receivables';
} else if (this.type === PlateType.FinanceReceivingorder) {
postParams['t'] = 'receivingorder';
}
AjaxHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
wself.deleteMainList();





wself.$router.go(-2);
}
});
},
deleteMainList() {
var index = -1;
for (var i = 0; i < this.crmListInfo.list.length; i++) {
var item = this.crmListInfo.list[i];
if (item.id === this.$route.params.id) {
index = i;
}
}

if (index >= 0) {
this.crmListInfo.list.splice(index, 1);
this.$store.commit('updateCrmListInfo', this.crmListInfo);
}
},
clickItem(item) {
if (item.type.toString() === PlateType.Contacts || item.type.toString() === PlateType.Customer || item.type.toString() === PlateType.Business || item.type.toString() === PlateType.Contract || item.type.toString() === PlateType.FinanceReceivables) {
if (!isNullValue(item.id) && item.id !== '0') {
this.$router.push({
name: 'maindetail',
params: {
id: item.id,
type: item.type.toString()
}
});
}
} else if (item.type.toString() === PlateType.AddressBook) {
if (!isNullValue(item.id) && item.id !== '0') {
this.$router.push({
name: 'user',
params: {
id: item.id
}
})
}
}
}
}
}




