

import Vue from 'vue'
import App from './App'
import router from './router'
import { sync } from 'vuex-router-sync'
import VueScroller from 'vue-scroller'
import VueLazyload from 'vue-lazyload'
import './js/GeoUtils_min.js'

import Vuex from 'vuex'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import { arrContains } from './js/utility.js'

Vue.use(DatetimePlugin)
Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)
Vue.use(router)
Vue.use(Vuex)

Vue.use(VueScroller)
Vue.use(VueLazyload, {
preLoad: 1.3,
error: require("./assets/head_error.png"),
loading: require("./assets/head_error.png"),
attempt: 0
})
Vue.config.productionTip = false

let store = new Vuex.Store({
state: {
direction: 'forward',
userInfo: '',
welcomeInfo: {}, //缓存redirect等信息。登陆成功从这里进入主页面

dynamicList: {},


newsDetail: [],


crmDetail: '',
crmListInfo: {
type: '',
list: [],
p: 1,
scroll: 0,
add: false, //是否有添加权限
loadingState: 0, //0未完成 2 完成加载 0: stop, 1: loading, 2: stopping loading
},
filtrateDetail: {}, //筛选页面数据
crmSearchListInfo: {
type: '',
list: [],
p: 1,
scroll: 0,
loadingState: 0, //0未完成 2 完成加载 0: stop, 1: loading, 2: stopping loading
},
addCustomInfos: [], //自定义添加数据
addCustomInfosScroll: 0, //自定义添加页面滚动
addRelateInfos: [], //增加相关数据
crmDetailInfo: {}, //动态详情的缓存
crmRelateDetailInfo: { //相关详情
type: '', //平台类型与type相同 进行赋值
id: '',
detail: {},
tabType: 0,
tabContenList: [],
p: 1,
scroll: 0,
refresh: false, //是否刷新tabContenList数组
loadingState: 0,
appendInfo: '', //附加信息 例如合同的回款等
},
crmShowDetail: {}, //CRM展示详情 用于编辑使用
fileUrl: '',
array: [], //回款历史需要
detail: '', //普通字典型数据缓存 记录添加页面类型 日程编辑用里面还有type 任务的添加和编辑信息 内勤页面
detailView: '', //记录日程展示页面 客户下签到记录客户信息
detailSecond: '', //下一页详情 签到多页面缓存 //统计页面
addFinanceInfos: {}, //增加财务数据
checkSetInfo: {}, //考勤设置页面
checkStatisInfo: {}, //考勤统计页面



logListInfo: {
listItem: [],
listFile: [],
showFileList: false,
p: 1,
scroll: 0,
loadingState: 0, //0未完成 2 完成加载 0: stop, 1: loading, 2: stopping loading
refresh: false, //添加成功后需要刷新数组
},
logDetail: '', //日志详情
addressBook: [], // 通讯录列表数据
examineHandle: [], //审批添加编辑操作数据
examineList: {}, //审批列表 create（我发起的） me_examine（待我审批） examined（我已审批）
projectList: {}, //任务大列表
taskList: {}, //任务小列表
examineMain: {},
scheduleMain: {}, //日程主页面
taskDetail: {}, //任务详情

xheaderRightOptions: {
showMore: false
},
xheaderShowAdd: false,
},
mutations: {
setUserInfo(state, userInfo) {
state.userInfo = userInfo;
},
updateDirection(state, payload) {
state.direction = payload.direction
},
updateWelcomeInfo(state, payload) {
state.welcomeInfo = payload
},


updateDynamicList(state, payload) {
state.dynamicList = payload
},


updateNewsDetail(state, payload) {
state.newsDetail = payload
},


updateCrmDetail(state, payload) {
state.crmDetail = payload
},
updateCrmListInfo(state, payload) {
state.crmListInfo = payload
},
updateCrmSearchListInfo(state, payload) {
state.crmSearchListInfo = payload
},
updateAddCustomInfos(state, payload) {
state.addCustomInfos = payload
},
updateCustomInfoItem(state, payload) { //index   item
Vue.set(state.addCustomInfos[payload.section], payload.row, payload.item);
},
updateAddRelateInfos(state, payload) { //相关数据的保存更新
state.addRelateInfos = payload
},
updateRelateInfoItem(state, payload) { //index   item
Vue.set(state.addRelateInfos, payload.row, payload.item);
},
updateAddCustomInfosScroll(state, payload) {
state.addCustomInfosScroll = payload
},


updateCrmDetailInfo(state, payload) {
state.crmDetailInfo = payload
},
updateCrmDetailInfoRefresh(state, payload) {
Vue.set(state.crmDetailInfo, 'refresh', payload);
},
updateCrmShowDetail(state, payload) {
state.crmShowDetail = payload
},
updateCrmRelateDetailInfo(state, payload) {
state.crmRelateDetailInfo = payload
},
updateCrmRelateDetailInfoRefresh(state, payload) {
Vue.set(state.crmRelateDetailInfo, 'refresh', payload);
},
updateFileUrl(state, payload) {
state.fileUrl = payload
},
updateArray(state, payload) {
state.array = payload
},
updateDetail(state, payload) {
state.detail = payload
},
updateDetailView(state, payload) {
state.detailView = payload
},
updateDetailSecond(state, payload) {
state.detailSecond = payload
},
updateAddFinanceInfos(state, payload) {
state.addFinanceInfos = payload
},
updateCheckSetInfo(state, payload) {
state.checkSetInfo = payload
},
updateCheckStatisInfo(state, payload) {
state.checkStatisInfo = payload
},


updateLogListInfo(state, payload) {
state.logListInfo = payload
},
updateLogDetail(state, payload) {
state.logDetail = payload
},
updateAddressBook(state, payload) {
state.addressBook = payload
},
updateExamineMain(state, payload) {
state.examineMain = payload
},
updateExamineHandle(state, payload) {
state.examineHandle = payload
},
updateExamineList(state, payload) {
state.examineList = payload
},
updateScheduleMain(state, payload) {
state.scheduleMain = payload
},
updateProjectList(state, payload) {
state.projectList = payload
},
updateTaskList(state, payload) {
state.taskList = payload
},
updateTaskDetail(state, payload) {
state.taskDetail = payload
},


updateXheaderRightOptions(state, payload) {
Vue.set(state.xheaderRightOptions, 'showMore', payload);
},
updateXheaderShowAdd(state, payload) {
state.xheaderShowAdd = payload
},
},
actions: {
setUserInfo({ commit }, user) {
commit('setUserInfo', user);
}
}
})

Vue.use(store)
sync(store, router)


const history = window.sessionStorage
history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)
history.setItem('/welcome/default', 0)
let isPush = false
let endTime = Date.now()
let methods = ['push', 'go', 'replace', 'forward', 'back']

document.addEventListener('touchend', () => {
endTime = Date.now()
})
methods.forEach(key => {
let method = router[key].bind(router)
router[key] = function(...args) {
isPush = true
method.apply(null, args)
}
})








router.beforeEach((to, from, next) => {
if (store.state.xheaderRightOptions.showMore) { //重置更多按钮
store.commit('updateXheaderRightOptions', false);
}

if (store.state.xheaderShowAdd) { //重置添加按钮
store.commit('updateXheaderShowAdd', false);
}



const toIndex = history.getItem(to.path)
const fromIndex = history.getItem(from.path)

if (arrContains(['/dynamic', '/news', '/', '/office', '/my'], to.path) && arrContains(['/dynamic', '/news', '/', '/office', '/my'], from.path)) { //TAB切换
historyCount = 0;
store.commit('updateDirection', { direction: 'none' })
history.setItem('count', historyCount)
history.setItem(to.path, historyCount)
} else if (arrContains(['/dynamic', '/news', '/', '/office', '/my'], to.path)) { //重置所有信息
historyCount = 0;
history.clear()
history.setItem('/welcome/default', 0)

if (!isPush && (Date.now() - endTime) < 377) {
store.commit('updateDirection', { direction: 'none' })
} else {
store.commit('updateDirection', { direction: 'reverse' })
}
history.setItem('count', historyCount)
history.setItem(to.path, historyCount)
} else if (toIndex) {

if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
store.commit('updateDirection', { direction: 'forward' })
} else if (toIndex === fromIndex) { //同层级的切换 类似内勤 或者tabbar
store.commit('updateDirection', { direction: 'none' })
} else {

if (!isPush && (Date.now() - endTime) < 377) {
store.commit('updateDirection', { direction: 'none' })
} else {
store.commit('updateDirection', { direction: 'reverse' })
}
}
} else {

if (arrContains(['/checkstatistics', '/checkset', '/teamsign'], to.path)) { //同层级的 historyCount 相同
store.commit('updateDirection', { direction: 'none' })
history.setItem('count', historyCount)
history.setItem(to.path, historyCount)
} else {
++historyCount
history.setItem('count', historyCount)
to.path !== '/' && history.setItem(to.path, historyCount)
store.commit('updateDirection', { direction: 'forward' })
}
}

if (window.localStorage.system_name && document.title !== window.localStorage.system_name) { //设置title
document.title = window.localStorage.system_name;
}
if (store.state.userInfo === '' && typeof window.localStorage.user !== 'undefined') {
window.sessionStorage.user = window.localStorage.user;
store.dispatch('setUserInfo', JSON.parse(window.localStorage.user));
}
if (to.matched.some(record => record.meta.requiresAuth)) {
if (store.state.userInfo !== '' && typeof store.state.userInfo !== 'undefined') {
next();
} else {
next({
path: '/welcome/default',
query: { redirect: to.fullPath }
});
}
} else {
next();
}

if (/\/http/.test(to.path)) {
let url = to.path.split('http')[1]
window.location.href = `http${url}`
} else {
next()
}
});

router.afterEach(function (to) {
isPush = false
})


/* eslint-disable no-new */
new Vue({
el: '#app',
store,
router,
template: '<App/>',
components: { App }
})



