import PlateType from '../../../js/enum.js'
import { customItem } from '../../../js/utility.js'
export function getCommunicationAddInfo(plateType, data) { //data 跟进类型数据
  var tempList = [];
  if (plateType === PlateType.Leads) {
    tempList.push(customItem('跟进内容', 'content', "textarea", "1"));
  } else if (plateType === PlateType.Customer || plateType === PlateType.CustomerPool) {
    tempList.push(customItem('跟进内容', 'content', "textarea", "1"));
    tempList.push(customItem('跟进类型', 'status_id', "select", "0", '', '', data));
    tempList.push(customItem('下次联系时间', 'nextstep_time', "datetime", "0", '', '', 'YYYY-MM-DD HH:mm'));
    tempList.push(customItem('相关商机', 'id', "business", "0"));
  } else if (plateType === PlateType.Business) {
    tempList.push(customItem('跟进内容', 'content', "textarea", "1"));
    tempList.push(customItem('跟进类型', 'type', "select", "0", '', '', data));
    tempList.push(customItem('下次联系时间', 'nextstep_time', "datetime", "0", '', '', 'YYYY-MM-DD HH:mm'));
  }
  return tempList;
}

export function getReceivablesAddInfo(name, data, userInfo) { //data 跟进类型数据
  var tempList = [];
  tempList.push(customItem('相关合同', 'contract_id', "text", "1", data.contract_id, data.contract_name, '', true));
  tempList.push(customItem('客户', 'customer_id', "text", "1", data.customer_id, data.customer_name, '', true));
  tempList.push(customItem('应收款编号', 'name', "text", "1", name, name));
  tempList.push(customItem('收款金额', 'price', "text", "1"));
  tempList.push(customItem('收款时间', 'pay_time', "datetime", "1", '', '', 'YYYY-MM-DD'));
  tempList.push(customItem('收款人', 'owner_role_id', "user", "1", userInfo.role_id, userInfo.name));
  tempList.push(customItem('描述', 'description', "textarea", "1"));
  return tempList;
}

export function getReceivingorderAddInfo(data, userInfo) { //data 跟进类型数据   回款单
  var tempList = [];
  tempList.push(customItem('回款人', 'owner_role_id', "user", "1", userInfo.role_id, userInfo.name));
  tempList.push(customItem('账户名称', 'account_id', "select", "1", '', '', data));
  tempList.push(customItem('账号/卡号', 'receipt_account', "text", "1", '', '', '', true));
  tempList.push(customItem('账户类别', 'company', "text", "1", '', '', '', true));
  tempList.push(customItem('回款金额', 'money', "text", "1"));
  tempList.push(customItem('回款时间', 'pay_time', "datetime", "1", '', '', 'YYYY-MM-DD'));
  tempList.push(customItem('描述', 'description', "textarea", "1"));
  return tempList;
}



