export function objToString(o) { //对象转字符串用于打印
  var r = [];
  if (typeof o == "string") {
    return "\"" + o.replace(/([\'\"\\])/g, "\\$1").replace(/(\n)/g, "\\n").replace(/(\r)/g, "\\r").replace(/(\t)/g, "\\t") + "\"";
  }
  if (typeof o == "object") {
    if (!o.sort) {
      for (var i in o) {
        r.push(i + ":" + objToString(o[i]));
      }
      if (!!document.all && !/^\n?function\s*toString\(\)\s*\{\n?\s*\[native code\]\n?\s*\}\n?\s*$/.test(o.toString)) {
        r.push("toString:" + o.toString.toString());
      }
      r = "{" + r.join() + "}";
    } else {
      for (var i = 0; i < o.length; i++) {
        r.push(objToString(o[i]))
      }
      r = "[" + r.join() + "]";
    }
    return r;
  }
  return o.toString();
}


export function isNullValue(value) { //判断字符串是不是空字符串
  if (value === null || value === undefined || value === '') {
    return true;
  }
  return false;
}

export function isPostNullValue(value) { //判断数据是上传空值  包含[]
  if (value === null || value === undefined || value === '' || JSON.stringify(value) === "[]") {
    return true;
  }
  return false;
}

export function getYMDFormateTimestamp(time) {
  time = time + ' 00:00:00';
  var t = time.split(/[- :]/);
  var d = new Date(t[0], t[1] - 1, t[2], t[3], t[4], t[5]);
  var timestamp = new Date(d).getTime().toString();
  timestamp = timestamp.substr(0, 10);
  return timestamp;
}

export function getTimestamp(date) {
  var timestamp = date.getTime().toString();
  timestamp = timestamp.substr(0, 10);
  return timestamp;
}

export function getYMDHMFormateTimestamp(time) {
  if (time === '' || time === '0' || isNullValue(time)) {
    return '';
  }
  time = time + ':00';
  var t = time.split(/[- :]/);
  var d = new Date(t[0], t[1] - 1, t[2], t[3], t[4], t[5]);
  var timestamp = new Date(d).getTime().toString();
  timestamp = timestamp.substr(0, 10);
  return timestamp;
}

export function getYMFormateTime(time) { //等到年月日 格式化时间
  if (time === '' || time === '0' || isNullValue(time)) {
    return '';
  }
  var times = 0;
  if (time.length === 13) {
    times = parseInt(time);
  } else {
    times = parseInt(time) * 1000;
  }
  var date = new Date(times); //如果date为13位不需要乘1000
  var Y = date.getFullYear() + '-';
  var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);
  return Y + M;
}

