export function isArrayType(value) { //变量是数组Array类型
  if (typeof Array.isArray === "function") {
    return Array.isArray(value);
  } else {
    return Object.prototype.toString.call(value) === "[object Array]";
  }
}

export function copyArr(arr) { //复制数组
  var _arr = [];
  for (var i = 0; i < arr.length; i++) {
    _arr[i] = arr[i];
  }
  return _arr;
}

export function copyObject(oldObj) {
  var newObj = oldObj;
  if (oldObj && typeof oldObj === 'object') {
    newObj = Object.prototype.toString.call(oldObj) === "[object Array]" ? [] : {};
    for (var i in oldObj) {
      newObj[i] = copyObject(oldObj[i]);
    }
  }
  return newObj;
}

export function arrContains(arr, obj) {
  var i = arr.length;
  while (i--) {
    if (arr[i] === obj) {
      return true;
    }
  }
  return false;
}

export function getImageUrl(url) { //拼接图片路径substr
  if (isNullValue(url)) { url = '' }
  var temp = '';
  if (url.length > 2) {
    temp = url.substr(0, 2);
  }
  if (temp === './') {
    url = url.substr(2, url.length - 2);
  }
  return window.localStorage.wkurl + url;
}

export function callPhone(phone) { //打电话
  if (window.cordova) {
    cordova.InAppBrowser.open('tel:' + phone, '_system');
  } else {
    window.location.href = 'tel:' + phone;
  }

}

import PlateType from './enum.js'
export function getModuleKeyword(type) {
  if (type === PlateType.Leads) {
    return 'leads';
  } else if (type === PlateType.Customer) {
    return 'customer';
  } else if (type === PlateType.CustomerPool) {
    return 'customer';
  } else if (type === PlateType.Business) {
    return 'business';
  } else if (type === PlateType.Contract) {
    return 'contract';
  } else if (type === PlateType.Contacts) {
    return 'contacts';
  } else if (type === PlateType.Product) {
    return 'product';
  } else if (type === PlateType.Task) {
    return 'task';
  } else if (type === PlateType.FinanceReceivables || type === PlateType.FinanceReceivingorder) {
    return 'finance';
  }
  return '';
}

//默认添加数据的事项
export function customItem(name, field, form_type, is_null, fieldvalue, content, data, disabled) {
  if (typeof fieldvalue === 'undefined') {
    fieldvalue = ''
  }

  if (typeof content === 'undefined') {
    content = ''
  }

  if (typeof disabled === 'undefined') {
    disabled = false;
  }

  //data 里面放  选择的数据  或者  datetime类型的时间格式 
  return { 'field': field, 'name': name, "form_type": form_type, "is_null": is_null, 'fieldvalue': fieldvalue, 'content': content, 'data': data, 'disabled': disabled }
}

export function compressImage(file, quality, callback) {
  //quality 设置为0.3
  const reader = new FileReader();
  reader.onload = function(event) {
    var result = event.target.result;
    if (file.size > 204800 && file.type !== 'image/gif' && quality < 1) {
      const img = new Image();
      img.src = result;
      img.onload = function() {
        // 如果图片大于四百万像素，计算压缩比并将大小压至400万以下
        var initSize = img.src.length;
        var width = img.width;
        var height = img.height;

        var ratio;
        if ((ratio = width * height / 4000000) > 1) {
          ratio = Math.sqrt(ratio);
          width /= ratio;
          height /= ratio;
        } else {
          ratio = 1;
        }
        var canvas = document.createElement('canvas');
        canvas.width = width;
        canvas.height = height;
        // 铺底色
        var ctx = canvas.getContext('2d');
        ctx.fillStyle = '#fff';
        ctx.fillRect(0, 0, canvas.width, canvas.height);
        // 如果图片像素大于100万则使用瓦片绘制
        var count;
        if ((count = width * height / 1000000) > 1) {
          count = ~~(Math.sqrt(count) + 1);
          // 计算要分成多少块瓦片
          // 计算每块瓦片的宽和高
          var nw = ~~(width / count);
          var nh = ~~(height / count);
          var tCanvas = document.createElement('canvas');
          tCanvas.width = nw;
          tCanvas.height = nh;
          for (var i = 0; i < count; i++) {
            for (var j = 0; j < count; j++) {
              var tctx = tCanvas.getContext('2d');
              tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);

              ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
            }
          }
          tCanvas.width = tCanvas.height = 0;
        } else {
          ctx.drawImage(img, 0, 0, width, height);
        }
        // 进行最小压缩
        var ndata = canvas.toDataURL('image/jpeg', quality);
        console.log('压缩前：' + initSize);
        console.log('压缩后：' + ndata.length);
        console.log('压缩率：' + ~~(100 * (initSize - ndata.length) / initSize) + '%');
        canvas.width = canvas.height = 0;
        callback(ndata);
      };
    } else { //小于200K不需要压缩 直接返回
      callback(result);
    }
  };
  reader.readAsDataURL(file);
}

export function createBlob(result) { //根据date URL 创建blob 用于上传
  var arr = result.split(',');
  var mime = arr[0].match(/:(.*?);/)[1];
  var bstr = atob(arr[1]);
  var n = bstr.length;
  var u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new Blob([u8arr], { type: mime });
}

export function fileSize(size) {
  var size_int = size;
  if (typeof size == 'string' && size.constructor == String) {
    size_int = parseInt(size);
  }
  var formatSize;
  if (parseInt(size_int / 1024 / 1024) > 0) {
    formatSize = (size_int / 1024 / 1024).toFixed(2) + 'MB';
  } else if (parseInt(size_int / 1024) > 0) {
    formatSize = (size_int / 1024).toFixed(2) + 'kB';
  } else {
    formatSize = size_int + 'Byte';
  }
  return formatSize;
}

import { PLATFORM } from '../config.js'
export function openFile(url) {
  if (PLATFORM === 'web') {
    // this.$store.commit('updateFileUrl', url);
    // this.$router.push({
    //   name: 'filepreview'
    // });
    window.open(url, '_system');
  } else if (isAndroid()) {
    var onSuccess = function(data) {
      // alert('提醒: ' + data.message);
    };

    function onError(error) {
      // alert('提醒: ' + error.message);
    }
    window.cordova.plugins.FileOpener.openFile(url, onSuccess, onError);
  } else if (isIOS()) { //iOS和WEB版使用浏览器打开
    window.open(url, '_system');
  }
}



export function isOriginal() {
  return isAndroid() || isIOS();
}

export function isAndroid() {
  var u = navigator.userAgent;
  return u.indexOf('Android') > -1 || u.indexOf('Adr') > -1;
}
export function isIOS() {
  var u = navigator.userAgent;
  return !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);;
}

export function insertToken(data, self) {
  data.token = self.$store.state.userInfo.token
  return data;
}

