import PlateType from '../../../js/enum.js'
import { customItem, getYMDFormateTime } from '../../../js/utility.js'

export function getReceivablesAddInfo(name, userInfo) { //data 跟进类型数据
  var tempList = [];
  tempList.push(customItem('相关合同', 'contract_id', "contract", "1", '', '', ''));
  tempList.push(customItem('客户', 'customer_id', "customer", "1", '', '', '', true));
  tempList.push(customItem('应收款编号', 'name', "text", "1", name, name));
  tempList.push(customItem('收款金额', 'price', "text", "1"));
  tempList.push(customItem('收款时间', 'pay_time', "datetime", "1", '', '', 'YYYY-MM-DD'));
  tempList.push(customItem('收款人', 'owner_role_id', "user", "1", userInfo.role_id, userInfo.name));
  tempList.push(customItem('描述', 'description', "textarea", "1"));
  return tempList;
}

export function getReceivablesEditInfo(data) { //data 跟进类型数据
  var tempList = [];
  tempList.push(customItem('相关合同', 'contract_id', "contract", "1", data.contract.contract_id, data.contract.number, ''));
  tempList.push(customItem('客户', 'customer_id', "customer", "1", data.customer_id, data.customer_name, '', true));
  tempList.push(customItem('应收款编号', 'name', "text", "1", data.name, data.name));
  tempList.push(customItem('收款金额', 'price', "text", "1", data.price, data.price));
  tempList.push(customItem('收款时间', 'pay_time', "datetime", "1", getYMDFormateTime(data.pay_time), getYMDFormateTime(data.pay_time), 'YYYY-MM-DD'));
  tempList.push(customItem('收款人', 'owner_role_id', "user", "1", data.owner_role_id, data.owner_name));
  tempList.push(customItem('描述', 'description', "textarea", "1", data.description, data.description));
  return tempList;
}

export function getReceivablesShowInfo(data) { //data 跟进类型数据
  var tempList = [];
  tempList.push({ name: '应收款编号', val: data.name, type: 0, id: "" });
  tempList.push({ name: '合同编号', val: data.contract.number, type: parseInt(PlateType.Contract), id: data.contract.contract_id });
  tempList.push({ name: '所属客户', val: data.customer_name, type: parseInt(PlateType.Customer), id: data.customer_id });
  tempList.push({ name: '收款金额', val: data.price, type: 0, id: "" });
  tempList.push({ name: '收款时间', val: getYMDFormateTime(data.pay_time), type: 0, id: "" });
  tempList.push({ name: '收款人', val: data.owner_name, type: parseInt(PlateType.AddressBook), id: data.owner_role_id });
  tempList.push({ name: '创建人', val: data.creator_name, type: parseInt(PlateType.AddressBook), id: data.creator_role_id });
  tempList.push({ name: '创建时间', val: getYMDFormateTime(data.create_time), type: 0, id: "" });
  tempList.push({ name: '描述', val: data.description, type: 0, id: "" });
  return tempList;
}

export function getReceivingorderAddInfo(data, userInfo) { //data 跟进类型数据   回款单
  var tempList = [];
  tempList.push(customItem('应收款', 'receivables_id', "receivables", "1", '', ''));
  tempList.push(customItem('账户名称', 'account_id', "select", "1", '', '', data));
  tempList.push(customItem('账号/卡号', 'receipt_account', "text", "1", '', '', '', true));
  tempList.push(customItem('账户类别', 'company', "text", "1", '', '', '', true));
  tempList.push(customItem('回款金额', 'money', "text", "1"));
  tempList.push(customItem('回款人', 'owner_role_id', "user", "1", userInfo.role_id, userInfo.name));
  tempList.push(customItem('回款时间', 'pay_time', "datetime", "1", '', '', 'YYYY-MM-DD'));
  tempList.push(customItem('描述', 'description', "textarea", "1"));
  return tempList;
}

export function getReceivingorderEditInfo(list, data) { //data 跟进类型数据   回款单
  var tempList = [];
  tempList.push(customItem('应收款', 'receivables_id', "receivables", "1", data.receivables_id, data.receivables_name));
  tempList.push(customItem('账户名称', 'account_id', "select", "1", data.bank_account_id, '', list));
  tempList.push(customItem('账号/卡号', 'receipt_account', "text", "1", data.receipt_account, data.receipt_account, '', true));
  tempList.push(customItem('账户类别', 'company', "text", "1", data.company, data.company, '', true));
  tempList.push(customItem('回款金额', 'money', "text", "1", data.money, data.money));
  tempList.push(customItem('回款人', 'owner_role_id', "user", "1", data.owner_role_id, data.owner_name));
  tempList.push(customItem('回款时间', 'pay_time', "datetime", "1", getYMDFormateTime(data.pay_time), getYMDFormateTime(data.pay_time), 'YYYY-MM-DD'));
  tempList.push(customItem('描述', 'description', "textarea", "1", data.description, data.description));
  return tempList;
}

export function getReceivingorderShowInfo(data) { //data 跟进类型数据
  var tempList = [];
  tempList.push({ name: '回款编号', val: data.name, type: 0, id: "" });
  tempList.push({ name: '应收款编号', val: data.receivables_name, type: parseInt(PlateType.FinanceReceivables), id: data.receivables_id });
  tempList.push({ name: '回款时间', val: getYMDFormateTime(data.pay_time), type: 0, id: "" });
  tempList.push({ name: '回款金额', val: data.money, type: 0, id: "" });
  tempList.push({ name: '回款人', val: data.owner_name, type: parseInt(PlateType.AddressBook), id: data.owner_role_id });
  tempList.push({ name: '创建人', val: data.creator_name, type: parseInt(PlateType.AddressBook), id: data.creator_role_id });
  tempList.push({ name: '账户名称', val: data.receipt_bank, type: 0, id: "" });
  tempList.push({ name: '账号/卡号', val: data.receipt_account, type: 0, id: '' });
  tempList.push({ name: '账户类别', val: data.company, type: 0, id: "" });
  tempList.push({ name: '创建时间', val: getYMDFormateTime(data.create_time), type: 0, id: "" });
  tempList.push({ name: '描述', val: data.description, type: 0, id: "" });
  return tempList;
}



