
import { AjaxSilentHttp, AjaxHttp } from '../../../js/ajaxHttp'
import { getYMDFormateTime, isArrayType, isPostNullValue } from "../../../js/utility.js"
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import Value2nameFilter from 'vux/src/filters/value2name.js'

import PlateType from '../../../js/enum.js'
import $ from 'jquery'



const on_navTabIndex_change = 'on-navTabIndex-change'
const on_rightShow_change = 'on-rightShow-change'
const on_sceneIndex_change = 'on-sceneIndex-change'
const on_leftIndex_change = 'on-leftIndex-change'
const on_scenePostInfo_change = 'on-scenePostInfo-change'
const on_scrollTop_change = 'on-scrollTop-change'
const on_sure = 'on-sure'

export default {
name: 'filtrate-view',
components: {
Group,
Cell,
XInput,
Datetime,
XAddress,
ChinaAddressV3Data
},
computed: {



},
props: {
CRMType: {
type: String,
default: ''
},
showScene: { //右侧布局是否展示
type: Boolean,
default: true
},
filtrateData: {
type: Array,
default: function() {
return []
}
}, //筛选数据
sceneData: {
type: Array,
default: function() {
return []
}
},
leftData: {
type: Array,
default: function() {
return []
}
},

navTabIndexM: { //0 左边的信息 1 右边的信息
type: Number,
default: -1
},
rightShowM: { //右侧布局是否展示
type: Boolean,
default: false
},
sceneIndexM: { //场景选择索引
type: Number,
default: -1
},
leftIndexM: { //左侧列表的索引
type: Number,
default: -1
},
scenePostInfoM: { //场景可能post的信息
type: Object,
default: function() {
return {}
}
},
filtrateScrollTopM: {
type: Number,
default: 0
},
},
watch: {
navTabIndex: function(val) {
this.$emit(on_navTabIndex_change, val);
},
rightShow: function(val) {
this.$emit(on_rightShow_change, val);
},
sceneIndex: function(val) {
this.$emit(on_sceneIndex_change, val);
},
leftIndex: function(val) {
this.$emit(on_leftIndex_change, val);
},
scenePostInfo: function(val) {
this.$emit(on_scenePostInfo_change, val);
},
navTabIndexM: function(val) {
if (this.navTabIndex !== val) {
this.navTabIndex = val;
}
},
rightShowM: function(val) {
if (this.rightShow !== val) {
this.rightShow = val;
}
},
sceneIndexM: function(val) {
if (this.sceneIndex !== val) {
this.sceneIndex = val;
}
},
leftIndexM: function(val) {
if (this.leftIndex !== val) {
this.leftIndex = val;
}
},
scenePostInfoM: function(val) {
if (this.scenePostInfo !== val) {
this.scenePostInfo = val;
}
},
filtrateScrollTopM: function(val) {
this.$nextTick(function() {
$('#scroll_area').scrollTop(val); 
})
},
},
data() {
return {
navTabIndex: -1, //0 左边的信息 1 右边的信息
navTabList: [{
name: '排序',
normalImage: require('../../../assets/sort_normal.png'),
selectImage: require('../../../assets/sort_select.png'),
normalColor: '#D0D3DB',
selectColor: '#0085CD',
type: 0
}, {
name: '筛选',
normalImage: require('../../../assets/filter_normal.png'),
selectImage: require('../../../assets/filter_select.png'),
normalColor: '#D0D3DB',
selectColor: '#0085CD',
type: 1
}],







leftIndex: -1,
leftShow: false,
rightShow: false,

sceneIndex: -1,
scenePostInfo: {}, //场景可能post的信息

datePickerVisibility: false, //控制datepicker展示
dateItem: null,
dateValue: '',
addressData: ChinaAddressV3Data,

filtrateScrollTop: 0, //页面滚动距离
}
},
mounted() {},
methods: {
navTabClick(item) { //点击左右类别筛选类按钮
if (this.navTabIndex === item.type) { //都要关闭
this.handleLeftRightShow(item.type, false);
this.navTabIndex = -1;
} else {
if (this.navTabIndex !== -1) {
this.handleLeftRightShow(this.navTabIndex, false); 
}
this.handleLeftRightShow(item.type, true); 
this.navTabIndex = item.type;
}
},
leftClick(index) {
if (this.leftIndex === index) {
this.leftIndex = -1;
} else {
this.leftIndex = index;
}
this.navTabIndex = -1;
this.leftShow = false;

var filtrateParams = this.getRightParmas();
var leftParams = {};
if (this.leftIndex !== -1) {
leftParams = this.leftData[this.leftIndex].params;
} else {
leftParams = {};
}

this.$emit(on_sure, {
'right': filtrateParams,
'left': leftParams,
});
},
leftBackClick() {
this.leftShow = false;
this.navTabIndex = -1;
},
rightBackClick() {
this.filtrateScrollTop = $('#scroll_area').scrollTop(); 
this.rightShow = false;
this.navTabIndex = -1;
},
handleLeftRightShow(type, show) {
if (type === 0) {
this.leftShow = show;
} else if (type === 1) {
this.rightShow = show;
if (show) { //滚动到隐藏时保存的位置
this.$nextTick(function() {
$('#scroll_area').scrollTop(this.filtrateScrollTop); 
})
}
}
},
sceneClick(item, index) { //场景点击
this.clearBottomFiltrate();
if (this.sceneIndex === index) {
this.scenePostInfo = {};
this.sceneIndex = -1;
} else {
this.sceneIndex = index;
if (item.type === '1') {
this.scenePostInfo['by'] = item.by;
} else {
this.scenePostInfo = {};
AjaxHttp('POST', 'm=index&a=scene ', { scene_id: item.id }, this, function(res, wself, success) {
if (success) {
wself.handleSceneData(res.data);
}
});
}
}
},
filtrateSelect(item, aitem) {
if (item.fieldvalue === aitem.key) {
item.fieldvalue = '';
} else {
item.fieldvalue = aitem.key
}
},
handleSceneData(list) {
for (var i = 0; i < this.filtrateData.length; i++) {
var item = this.filtrateData[i];
for (var j = 0; j < list.length; j++) {
var smallItem = list[j];

if (smallItem.field === item.field) {
if (item.form_type === 'select' || item.form_type === 'checkbox' || item.form_type === 'text' || item.form_type === 'number' || item.form_type === 'floatnumber' || item.form_type === 'email' || item.form_type === 'mobile' || item.form_type === 'textarea') {
item.fieldvalue = smallItem.val;
} else if (item.form_type === 'datetime') {
var array = smallItem.val.split(" ");
if (array.length > 1) {
item.fieldvalue = array[0];
item.fieldvalue2 = array[1];
} else if (array.length === 1) {
item.fieldvalue = array[0];
}
} else if (item.form_type === 'address') {
var array = smallItem.val.split(" ");
if (array.length > 3) {
item.fieldvalue = array.slice(0, 3);
} else {
item.fieldvalue = array;
}
}
}
}
}
},
selectStartTime(item) {
this.dateItem = item;
this.dateItem.dateType = 'start';
this.datePickerVisibility = true;
},
selectEndTime(item) {
this.dateItem = item;
this.dateItem.dateType = 'end';
this.datePickerVisibility = true;
},
datePickerChange(value) { //datePicker 改变
if (this.dateItem.dateType === 'start') {
this.dateItem.fieldvalue = value;
} else if (this.dateItem.dateType === 'end') {
this.dateItem.fieldvalue2 = value;
}
},
datePickerConfirm() {
if (this.dateItem.dateType === 'start') {
this.dateItem.fieldvalue = this.dateValue;
} else if (this.dateItem.dateType === 'end') {
this.dateItem.fieldvalue2 = this.dateValue;
}
},
addressName(item) {
return Value2nameFilter(item.fieldvalue, ChinaAddressV3Data);
},
enterOwnerList(item, index) {
this.$emit(on_scrollTop_change, $('#scroll_area').scrollTop());
this.$router.push({
name: 'selectlist',
params: {
id: '-1',
type: this.CRMType,
selecttype: '2', //负责人
section: item.field,
row: '-1',
}
});
},
clearFiltrate() {

this.sceneIndex = -1;
this.scenePostInfo = {};

this.clearBottomFiltrate();
},
clearBottomFiltrate() {
for (var i = 0; i < this.filtrateData.length; i++) {
var item = this.filtrateData[i];
item['content'] = [];
if (item.form_type === 'address') {
item['fieldvalue'] = [];
item['showAddress'] = false;
} else if (item.form_type === 'datetime') { //添加的时候 因为合同选择不包含具体时间所以加入 form_addition 属性 辅助控制
item['fieldvalue'] = '';
item['fieldvalue2'] = '';
} else {
item['fieldvalue'] = '';
}
}
},
sureFiltrate() {
this.$emit(on_scrollTop_change, $('#scroll_area').scrollTop());
var filtrateParams = this.getRightParmas();
this.rightBackClick();

var leftParams = {};
if (this.leftIndex !== -1) {
leftParams = this.leftData[this.leftIndex].params;
} else {
leftParams = {};
}

this.$emit(on_sure, {
'right': filtrateParams,
'left': leftParams,
});
},
getRightParmas() { //获取右侧列表上传所需参数
var filtrateParams = {};
for (var i = 0; i < this.filtrateData.length; i++) {
var item = this.filtrateData[i];
if ((item.form_type === 'text' || item.form_type === 'number' || item.form_type === 'floatnumber' || item.form_type === 'email' || item.form_type === 'mobile' || item.form_type === 'textarea') && item.fieldvalue !== '') {
filtrateParams[item.field + '[condition]'] = 'contains';
filtrateParams[item.field + '[value]'] = item.fieldvalue;
} else if ((item.form_type === 'select' || item.form_type === 'checkbox') && item.fieldvalue !== '') {
filtrateParams[item.field + '[value]'] = item.fieldvalue;
} else if (item.form_type === 'address') {
var addresstempvalue = this.addressName(item);
if (addresstempvalue !== '') {
var addressfieldvalue = addresstempvalue === '' ? [] : addresstempvalue.split(" ");
if (addressfieldvalue.length >= 1) {
filtrateParams['address[state]'] = addressfieldvalue[0];
} else {
filtrateParams['address[state]'] = '';
}
if (addressfieldvalue.length >= 2) {
filtrateParams['address[city]'] = addressfieldvalue[1];
} else {
filtrateParams['address[city]'] = '';
}
if (addressfieldvalue.length >= 3) {
filtrateParams['address[area]'] = addressfieldvalue[2];
} else {
filtrateParams['address[area]'] = '';
}
filtrateParams['address[value]'] = '';
}
} else if (item.form_type === 'datetime' && (item.fieldvalue !== '' || item.fieldvalue2 !== '')) {
filtrateParams['nextstep_time[start]'] = item.fieldvalue;
filtrateParams['nextstep_time[end]'] = item.fieldvalue2;
} else if (item.form_type === 'user' && item.fieldvalue !== '') {
filtrateParams[item.field + '[value]'] = item.fieldvalue;
}
}

if (this.scenePostInfo.by) {
filtrateParams.by = this.scenePostInfo.by;
} else {
delete filtrateParams['by'];
}

return filtrateParams;
}
}
}




