export function AjaxImageHttp(type, url, data, self, success) {
  self.$vux.loading.show({
    text: LOAD_TITLE
  });

  if (typeof window.localStorage.wkurl === 'undefined' || window.localStorage.wkurl === '') {
    self.$vux.loading.hide();
    self.$router.push({
      path: '/welcome/default',
      query: { redirect: self.$route.fullPath }
    })
    return;
  } else {
    data.append('token', self.$store.state.userInfo.token);
    $.ajax({
      type: type,
      url: window.localStorage.wkurl + 'vue.php?' + url,
      data: data,
      dataType: 'json',
      timeout: 15000,
      processData: false,
      contentType: false,
      success: (res) => {
        if (res.status === 1) {
          if (success) { success(res, self, true) }
        } else if (res.status === -1) {
          self.$vux.toast.text(res.info, 'top');
          if (PLATFORM !== 'web' && window.xgpush) {
            window.xgpush.unregisterPush();
          }
          self.$store.dispatch('setUserInfo', ''); //清空vuex缓存的信息
          self.$store.commit('updateDynamicList', {});
          self.$store.commit('updateCrmDetail', '');
          self.$store.commit('updateNewsDetail', []);
          self.$store.commit('updateCrmListInfo', {
            type: '',
            list: [],
            p: 1,
            scroll: 0,
            add: false, //是否有添加权限
            loadingState: 0, //0未完成 2 完成加载 0: stop, 1: loading, 2: stopping loading
          });
          self.$store.commit('updateLogListInfo', {
            listItem: [],
            listFile: [],
            showFileList: false,
            p: 1,
            scroll: 0,
            loadingState: 0, //0未完成 2 完成加载 0: stop, 1: loading, 2: stopping loading
            refresh: false, //添加成功后需要刷新数组
          });
          self.$store.commit('updateAddressBook', []);

          window.sessionStorage.removeItem("user"); //清空自动登录缓存的信息
          window.localStorage.removeItem("user");
          window.localStorage.removeItem("system_name");
          window.localStorage.removeItem("wkurl");

          self.$router.push({
            path: '/welcome/login',
            query: { redirect: self.$route.fullPath }
          })
          if (success) { success(res, self, false) };
        } else {
          self.$vux.toast.text(res.info, 'top');
          if (success) { success(res, self, false) }
        }
        self.$vux.loading.hide();
      },
      error: function(XMLHttpRequest, textStatus, errorThrown) {
        self.$vux.loading.hide();
        if (textStatus == "timeout") {
          self.$vux.toast.text("加载超时，请重试", 'top');
        } else {
          self.$vux.toast.text("请求出错", 'top');
        }
      }
    });
  }

}



