
import {
dropDown
} from 'iver'
import CommonCell from '../components/CommonCell'
import ListCell from '../components/ListCell'
import FiltrateView from '../components/FiltrateView'
import ContractCell from '../components/ContractCell.vue'
import BusinessCell from '../components/BusinessCell.vue'
import FreceivablesCell from '../components/FreceivablesCell.vue'
import FreceivingorderCell from '../components/FreceivingorderCell.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState } from 'vuex'
import event from '../../../js/Event.js'
import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import PlateType from '../../../js/enum.js'
import { getYMDFormateTime, isArrayType } from "../../../js/utility.js"

export default {
components: {
Grid,
GridItem,
Group,
Cell,
dropDown,
CommonCell,
ListCell,
BusinessCell,
FreceivablesCell, //财务下 应收款 列表
FreceivingorderCell, //财务下 回款单 列表
ContractCell,
FiltrateView
},
computed: {
...mapState({
route: state => state.route,
crmDetailInfo: state => state.crmDetailInfo,
crmListInfo: state => state.crmListInfo
}),
PlateType() {
return PlateType;
},
showProductSelect() {
return this.$route.params.action === 'productmultiple';
},
showFiltrate() {
return this.$route.params.action === 'default' && (this.$route.params.type === PlateType.Customer || this.$route.params.type === PlateType.CustomerPool || this.$route.params.type === PlateType.Contract || this.$route.params.type === PlateType.Leads || this.$route.params.type === PlateType.Contacts || this.$route.params.type === PlateType.Business);
},
showScene() { //是否展示场景
if (this.$route.params.type === PlateType.Contacts) {
return false;
}
return true;
},
leftData() {
var type = this.$route.params.type
if (type === PlateType.Customer || type === PlateType.CustomerPool || type === PlateType.Business) {
return [{ name: '创建时间正序', params: { order_type: 'asc', order_field: 'create_time' } },
{ name: '创建时间倒序', params: { order_type: 'desc', order_field: 'create_time' } },
{ name: '修改时间正序', params: { order_type: 'asc', order_field: 'update_time' } },
{ name: '修改时间倒序', params: { order_type: 'desc', order_field: 'update_time' } },
{ name: '下次联系时间正序', params: { order_type: 'asc', order_field: 'nextstep_time' } },
{ name: '下次联系时间倒序', params: { order_type: 'desc', order_field: 'nextstep_time' } }
];
} else if (this.type === PlateType.Contract) {
return [{ name: '签约时间正序', params: { order_type: 'asc', order_field: 'due_time' } },
{ name: '签约时间倒序', params: { order_type: 'desc', order_field: 'due_time' } },
{ name: '生效时间正序', params: { order_type: 'asc', order_field: 'start_date' } },
{ name: '生效时间倒序', params: { order_type: 'desc', order_field: 'start_date' } },
{ name: '到期时间正序', params: { order_type: 'asc', order_field: 'end_date' } },
{ name: '到期时间倒序', params: { order_type: 'desc', order_field: 'end_date' } },
{ name: '创建时间正序', params: { order_type: 'asc', order_field: 'create_time' } },
{ name: '创建时间倒序', params: { order_type: 'desc', order_field: 'create_time' } },
{ name: '修改时间正序', params: { order_type: 'asc', order_field: 'update_time' } },
{ name: '修改时间倒序', params: { order_type: 'desc', order_field: 'update_time' } }
];
} else if (type === PlateType.Leads || type === PlateType.Contacts) {
return [{ name: '创建时间正序', params: { order_type: 'asc', order_field: 'create_time' } },
{ name: '创建时间倒序', params: { order_type: 'desc', order_field: 'create_time' } },
{ name: '修改时间正序', params: { order_type: 'asc', order_field: 'update_time' } },
{ name: '修改时间倒序', params: { order_type: 'desc', order_field: 'update_time' } }
];
}
return [];
}
},
watch: {
filterNavIndex: function(val) {
if (val >= 0) {
var item = this.filterNameList[val];
this.filterNowList = item.items;
var filterMenuIndex = -1;
for (var i = 0; i < this.filterNowList.length; i++) {
var selectItem = this.filterNowList[i];
if (selectItem.id === this.filterSelectID) {
filterMenuIndex = i;
}
}
this.filterMenuIndex = filterMenuIndex;
}
},
},
data() {
return {
p: 1,
addPermission: false,
filterShow: false,
filterBackShow: false, //产品过滤半透明背景
type: '',
list: [],
pullupEnabled: true,
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
filterNavIndex: -1, //导航索引
filterMenuIndex: -1, //菜单选择项
filterNameList: [], //筛选顶部名称 并且就自身索引位置
filterTotalList: [], //全部数据
filterNowList: [], // 当前展示的数据
filterSelectID: '', //请求数据用
selectItemList: [], //选中的数组

filtrateData: [], //筛选数据
sceneData: [], //场景信息
navTabIndex: -1, //0 左边的信息 1 右边的信息
rightShow: false, //右侧布局是否展示
leftIndex: -1, //左侧列表的选择 默认不选择 -1
sceneIndex: -1, //场景选择索引
scenePostInfo: {}, //场景可能post的信息
filtrateScrollTop: 0, //滚动距离指右边的布局

filtratePostParams: {}, //筛选参数
}
},
mounted() {
this.initInfo();
},
beforeDestroy() {
event.$off("gotoAddData");
event.$off("filterData"); 
},
beforeRouteLeave(to, from, next) {

if (this.$route.params.action === 'default') {
if (to.name === 'maindetail' || to.name === 'crmdetail' || to.name === 'adddata' || to.name === 'crmsearch' || to.name === 'relevanceaproduct' || to.name === 'selectlist' || to.name === 'financeadd') { //selectlist 筛选调整负责人
var showMainFilter = false; 
if (to.name === 'selectlist') { //跳转选择列表(负责人)
showMainFilter = true;
event.$on('selectedRelateItem', function(data) {
this.handleSelectedInfo(data);
}.bind(this));
}

this.$store.commit('updateCrmListInfo', {
type: this.$route.params.type, //平台类型与type相同 进行赋值
list: this.list,
p: this.p,
scroll: this.$refs.my_scroller.getPosition().top,
add: this.addPermission,
topRefresh: false, //下拉刷新（添加成功）
loadingState: this.$refs.my_scroller.loadingState,

filterNavIndex: this.filterNavIndex, //导航索引
filterMenuIndex: this.filterMenuIndex, //菜单选择项
filterNameList: this.filterNameList, //筛选顶部名称 并且就自身索引位置
filterTotalList: this.filterTotalList, //全部数据
filterNowList: this.filterNowList, // 当前展示的数据
filterSelectID: this.filterSelectID, //请求数据用
selectItemList: this.selectItemList, //选中的数组


filtrateData: this.filtrateData,
sceneData: this.sceneData,
navTabIndex: this.navTabIndex,
rightShow: this.rightShow,
leftIndex: this.leftIndex,
sceneIndex: this.sceneIndex,
scenePostInfo: this.scenePostInfo,
filtrateScrollTop: this.filtrateScrollTop,
filtratePostParams: this.filtratePostParams,

});

} else {
this.$store.commit('updateCrmListInfo', {
type: '', //平台类型与type相同 进行赋值
list: [],
p: 1,
scroll: 0,
add: false,
loadingState: 0
});
}
}


next();
},
methods: {
initInfo() {
event.$on('gotoAddData', function() {
if (this.$route.params.type === PlateType.FinanceReceivables || this.$route.params.type === PlateType.FinanceReceivingorder) {
this.$store.commit('updateAddFinanceInfos', []);
this.$router.push({ name: 'financeadd', params: { type: this.type, action: 'default', id: '0' } });
} else {
this.$store.commit('updateAddCustomInfos', []); 
this.$store.commit('updateAddCustomInfosScroll', 0);
this.$router.push({ name: 'adddata', params: { type: this.type, action: 'default', id: '0' } });
}
}.bind(this));


event.$on('filterData', function() {
this.filterData();
}.bind(this));

this.type = this.$route.params.type;
if (this.crmListInfo.type === this.type) {
if (this.crmListInfo.add) {
this.addPermission = this.crmListInfo.add;
this.$store.commit('updateXheaderShowAdd', true);
}

if (this.$route.params.type === PlateType.Product) {
this.filterTotalList = this.crmListInfo.filterTotalList; 
this.filterNameList = this.crmListInfo.filterNameList; 
this.filterNavIndex = this.crmListInfo.filterNavIndex; 
this.filterMenuIndex = this.crmListInfo.filterMenuIndex; 
this.filterNowList = this.crmListInfo.filterNowList; 
this.filterSelectID = this.crmListInfo.filterSelectID; 
this.selectItemList = this.crmListInfo.selectItemList; 
}


this.filtrateData = this.crmListInfo.filtrateData;
this.sceneData = this.crmListInfo.sceneData;
if (!this.crmListInfo.topRefresh) {
this.list = this.crmListInfo.list;
this.p = this.crmListInfo.p;
}

this.$nextTick(() => {
this.navTabIndex = this.crmListInfo.navTabIndex;
this.rightShow = this.crmListInfo.rightShow;
this.leftIndex = this.crmListInfo.leftIndex;
this.sceneIndex = this.crmListInfo.sceneIndex;
this.scenePostInfo = this.crmListInfo.scenePostInfo;
this.filtrateScrollTop = this.crmListInfo.filtrateScrollTop;
this.filtratePostParams = this.crmListInfo.filtratePostParams;

if (!this.crmListInfo.topRefresh) {
this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, this.crmListInfo.scroll, false);
if (this.crmListInfo.loadingState === 2) {
this.$refs.my_scroller.finishInfinite(true);
}
}
});
}
},
infinite(done) {
this.getList(done);
},
refresh(done) {
this.p = 1;
this.getList(done);
},
getList(done) {
var url = '';
var postParams = {};
if (this.type === PlateType.Leads) {
url = 'm=leads&a=index';
} else if (this.type === PlateType.Customer) {
url = 'm=customer&a=index';
} else if (this.type === PlateType.CustomerPool) {
url = 'm=customer&a=index&content=resource';
} else if (this.type === PlateType.Business) {
url = 'm=business&a=index';
} else if (this.type === PlateType.Contract) {
url = 'm=contract&a=index';
} else if (this.type === PlateType.Contacts) {
url = 'm=contacts&a=index';
} else if (this.type === PlateType.Product) {
url = 'm=product&a=index';
} else if (this.type === PlateType.FinanceReceivables) {
url = 'm=finance&a=index';
postParams['t'] = 'receivables';
} else if (this.type === PlateType.FinanceReceivingorder) {
url = 'm=finance&a=index';
postParams['t'] = 'receivingorder';
}

if (this.$route.params.action === 'select') {
postParams['p'] = this.p;
if (this.type === PlateType.Contacts) { //进行选择操作 并且是 联系人 需要客户ID
postParams['customer_id'] = this.$route.params.addition;
} else if (this.type === PlateType.Business) { //进行联系人关联商机 （合同下关联商机） 传客户ID
postParams['customer_id'] = this.$route.params.addition.customer_id;
}



} else if (this.$route.params.type === PlateType.Product) {
postParams['p'] = this.p;
if (this.filterMenuIndex !== -1) {
postParams['category_id'] = this.filterSelectID;
}
} else {
if (this.showFiltrate) { //展示主筛选
postParams = this.filtratePostParams;
postParams.p = this.p;
} else {
postParams.p = this.p;
}
}

AjaxSilentHttp('POST', url, postParams, this, function(res, wself, success) {
if (success && isArrayType(res.list) && (wself.route.name === 'list' || wself.route.name === 'relatelist')) {

if (wself.$route.params.action === 'default' && !(wself.type === PlateType.CustomerPool || wself.type === PlateType.Product)) { //暂时强制 关闭 合同 客户池 产品的添加
var permission_list = res.permission_list;
if (typeof permission_list !== 'undefined' && permission_list.add === 1) {

wself.addPermission = true;
wself.$store.commit('updateXheaderShowAdd', true);
}
}

if (wself.type === PlateType.Product) { //在第一页的时候处理筛选数据
if (isArrayType(res.category_list) && wself.filterTotalList.length === 0) {
wself.filterTotalList = res.category_list;
wself.filterNameList.push({ name: "全部", items: wself.filterTotalList, select: -1 }); 
wself.filterNavIndex = 0;
wself.filterMenuIndex = -1;
}
}

var newItems = [];
for (var i = 0; i < res.list.length; i++) {
newItems[i] = wself.getNewItem(res.list[i]);
}

if (wself.p === 1) {

if (wself.showFiltrate &&  wself.filtrateData.length === 0 && isArrayType(res.fields_list)) {
if (isArrayType(res.scene_list)) {
wself.sceneData = res.scene_list;
} else {
wself.sceneData = [];
}
wself.handleFiltrateInfo(res.fields_list);
}
wself.list = newItems;
wself.p = wself.p + 1;
} else {
if (newItems.length > 0 && wself.list.length > 0) {
var tempItem = newItems[0];
var tempItem2 = wself.list[0];
if (tempItem.id !== tempItem2.id) {
wself.list = wself.list.concat(newItems);
wself.p = wself.p + 1;
}
}
}
if (typeof done === 'function') {
if (res.list.length < 10) {
done(true);
} else if (res.list.length === 0) {
done(true);
} else {
done();
}
}
}
});
},
selectCallback() {},
getNewItem(item) {
if (this.type === PlateType.Leads) {
return { title: item.name, detail: item.mobile === '' ? '无' : item.mobile, rightTitle: getYMDFormateTime(item.create_time), permission: item.permission, id: item.leads_id };
} else if (this.type === PlateType.Customer || this.type === PlateType.CustomerPool) {
return { title: item.name, detail: item.owner_name, rightTitle: getYMDFormateTime(item.create_time), permission: item.permission, id: item.customer_id };
} else if (this.type === PlateType.Business) {
return { title: item.name, detail: item.customer_name, rightTitle: getYMDFormateTime(item.create_time), rightDetail: item.final_price, percent: item.schedule, schedule: item.status_name, permission: item.permission, id: item.business_id };
} else if (this.type === PlateType.Contract) {
return { title: item.number, detail: item.customer_name, rightTitle: getYMDFormateTime(item.create_time), rightDetail: item.price, percent: parseInt(item.schedule) > 100 ? '100' : item.schedule.toString(), is_checked: item.is_checked, permission: item.permission, customer_id: item.customer_id, customer_name: item.customer_name, id: item.contract_id };
} else if (this.type === PlateType.Contacts) {
return { title: item.name, detail: item.customer_name, rightTitle: item.telephone, permission: item.permission, id: item.contacts_id };
} else if (this.type === PlateType.Product) {
var selected = false;
if (this.$route.params.action === 'productmultiple') {
for (var i = 0; i < this.selectItemList.length; i++) {
var selectItem = this.selectItemList[i];
if (selectItem.id === item.product_id) {
selected = true;
}
}
}
return { title: item.name, detail: item.standard === '' ? item.standard : '价格（元/' + item.standard + '）', rightTitle: item.suggested_price, standard: item.standard, permission: item.permission, id: item.product_id, selected: selected };
} else if (this.type === PlateType.FinanceReceivables) {
item.schedule = parseInt(item.schedule) > 100 ? '100' : item.schedule.toString();
item.id = item.receivables_id;
return item;
} else if (this.type === PlateType.FinanceReceivingorder) {
item.id = item.receivingorder_id;
return item;
}
return { top: '', left: '', right: '' }
},
handleFiltrateInfo(list) {
var tempArray = [];
for (var i = 0; i < list.length; i++) {
var item = list[i];

if (item.form_type === 'address') {
item['fieldvalue'] = [];
item['showAddress'] = false;
} else if (item.form_type === 'datetime') { //添加的时候 因为合同选择不包含具体时间所以加入 form_addition 属性 辅助控制
item['fieldvalue'] = '';
item['fieldvalue2'] = '';
} else {
item['fieldvalue'] = '';
}
tempArray.push(item);
}
this.filtrateData = tempArray;

},
enterDetail(item) { //进入详情
if (this.$route.params.action === 'default') {
if (this.type === PlateType.Product) {
this.$router.push({
name: 'crmdetail',
params: {
id: item.id,
type: this.type
}
});
} else {
this.$router.push({
name: 'maindetail',
params: {
id: item.id,
type: this.type
}
});
}
} else if (this.$route.params.action === 'select') {
event.$emit('selectedRelateItem', { type: this.type, fromtype: this.$route.params.fromtype, item: item, section: this.$route.params.section, row: this.$route.params.row, addition: this.$route.params.addition });
if (this.type === PlateType.Business && this.$route.params.fromtype === PlateType.Contacts) { //联系人下的关联商机跳转
this.$store.commit('updateCrmDetailInfoRefresh', true);
}
this.$router.go(-1);
}
},
filterData() { //暂时用于产品筛选
this.filterShow = !this.filterShow;
this.filterBackShow = !this.filterBackShow;
},
clickFilterBackShow() { //点击黑色背景
this.filterShow = false;
this.filterBackShow = false;
},
getSelectImage(selected) {
if (selected) {
return require('../../../assets/selected.png');
}
return require('../../../assets/un_selected.png');
},
isListHasInfo(item) {
return isArrayType(item) && (item.length > 0);
},
enterFilterNext(next, index, item) { //如果有下层 next 布尔值
if (next) {
this.filterNameList.push({ name: item.name, items: item.childs, select: -1 }); 
this.filterNavIndex = this.filterNavIndex + 1;
}
},
selectFilterItem(item, index) {
if (this.filterMenuIndex === index) { //重置为全部
this.filterMenuIndex = -1;
var filteritem = this.filterNameList[this.filterNavIndex];
filteritem.select = -1;
this.filterSelectID = '';
} else {
this.filterMenuIndex = index;
var filteritem = this.filterNameList[this.filterNavIndex];
filteritem.select = index;
this.filterSelectID = item.id;
}
this.clickFilterBackShow();
this.p = 1;
this.list = [];
this.$refs.my_scroller.finishInfinite(false);

},
currentSelect(index) {
if (this.filterNameList.length === 1) {
return true;
} else if (this.filterNameList.length - 1 === index) {
return true;
} else {
return false;
}
},
navSelect(index) {
this.filterNameList.splice(index + 1, this.filterNameList.length - index - 1);
this.filterNavIndex = index;
},
selectProduct(item) {
item.selected = !item.selected;
if (item.selected) {
this.selectItemList.push(item);
} else {
for (var i = 0; i < this.selectItemList.length; i++) {
var selectItem = this.selectItemList[i];
if (selectItem.id === item.id) {
this.selectItemList.splice(i, 1);
}
}
}
},
sure() { //产品选择确定页面
if (this.selectItemList.length === 0) {
this.$vux.toast.text('请先进行选择', 'top');
return;
}
this.$store.commit('updateArray', this.selectItemList);
this.$router.push({
name: 'relevanceaproduct' 
})
},

handleSelectedInfo(data) { //筛选跳转负责人返回的数据
var field = data.section;
var filtrateData = this.crmListInfo.filtrateData;
for (var i = 0; i < filtrateData.length; i++) {
var filtrateItem = filtrateData[i];
if (filtrateItem.field === field) {
filtrateItem.content = data.item.user_name;
filtrateItem.fieldvalue = data.item.role_id;
}
}
this.crmListInfo.filtrateData = filtrateData;
this.$store.commit('updateCrmListInfo', this.crmListInfo);
},
onNavTabIndexChange(val) {
if (this.navTabIndex !== val) {
this.navTabIndex = val;
}
},
onRightShowChange(val) {
if (this.rightShow !== val) {
this.rightShow = val;
}
},
onLeftIndexChange(val) {
if (this.leftIndex !== val) {
this.leftIndex = val;
}
},
onSceneIndexChange(val) {
if (this.sceneIndex !== val) {
this.sceneIndex = val;
}
},
onScenePostInfoChange(val) {
if (this.scenePostInfo !== val) {
this.scenePostInfo = val;
}
},
onScrollTopChange(val) { //主筛选右列表滚动
if (this.filtrateScrollTop !== val) {
this.filtrateScrollTop = val;
}
},
onFiltrateSure(data) { //主筛选点击确定
for (var key in data.left) {
data.right[key] = data.left[key];
}
this.filtratePostParams = data.right;
this.$refs.my_scroller.triggerPullToRefresh();
}
}
}




