export default {
  name: 'list-cell',
  components: {},
  computed: {
    checkColor() {
      if (this.rightDes === '0') {
        return '#F5CA00';
      } else if (this.rightDes === '1') {
        return '#7CCA4E';
      } else if (this.rightDes === '2') {
        return '#F5715F';
      }
      return '#FFF';
    },
    checkname() {
      if (this.rightDes === '0') {
        return '待审';
      } else if (this.rightDes === '1') {
        return '通过';
      } else if (this.rightDes === '2') {
        return '驳回';
      }
      return '#FFF';
    },
  },
  data() {
    return {}
  },
  props: {
    showArrow: Boolean, //是否展示小箭头
    title: String,
    showtitle: {
      type: Boolean,
      default: true
    },
    marginLeft: {
      type: Boolean,
      default: true
    },
    detail: String,
    detailColor: { //控制detail是否显示
      type: String,
      default: '#8D8D8D'
    },
    showdetail: { //控制detail是否显示
      type: Boolean,
      default: true
    },
    detailTypeShow: { //crm详情展示效果
      type: Boolean,
      default: false
    },
    showBottomLine: { //控制detail是否显示
      type: Boolean,
      default: false
    },
    showCenter: {
      type: Boolean,
      default: false
    },
    rightTitle: String,
    rightDetail: String,
    mark: String,
    rightDes: String,
  },
  mounted() {

  },
  methods: {

  }

}




